//jQuery No-Conflict
if(typeof jQuery != 'undefined'){
  jQuery.noConflict();
}
var T_DEBUG_MODE = false;
var T_FieldLabelMap = {};
var SF_API_VERSION = '53.0';
// Util
var T_Util    = new SD_Util();
// Logger 
var T_Logger  = new SD_Logger({ debugmode : T_DEBUG_MODE }); 
// Main app
var T_App;
// DebuglogViewer
var T_LogViewer;
// FieldMassEdit
var T_FieldMassEdit;

jQuery(document).ready(function(){
  var parser = T_Util.getParser(window.location.href);
  // Pre-fill issue body from cache
  if(T_Logger.isGithubIssue(parser)) {
    return T_Logger.prefillIssue();
  }
  // OAuth2 Callback
  if(T_Util.isOAuthCallback(parser)) {
    // Works on callback page
    var conn = new SD_OAuthConnect();
    var code = T_Util.getParam('code');
    //console.log('>>>> isOAuthCallback code', code);
    var sfdc_community_url = T_Util.getParam('sfdc_community_url');
    var tabid = T_Util.getParam('tabid');
    if(T_Util.isBlank(code)) return;
    jQuery('body').text('Processing...');
    //console.log('>>>> isOAuthCallback ', parser, 'tabid = ' + tabid);
    conn.authorize({ code : code, sfdc_community_url : sfdc_community_url }, function(data){
      if(data) {
        data['tabid'] = T_Util.isBlank(tabid) ? '' : tabid;
        chrome.runtime.sendMessage({type: 'SD_sfdcoauth',options : data},function(response) {
          window.close();
        });
      }
    });
    return;
  }
  // Inject js on Classic page
  if(!T_Util.isSfdcDomain(parser.hostname) || T_Util.isLightningDomain(parser)) return;

  // All Check
  if(T_Util.isProfileEditPage(parser)) {
    var allChecker = new SD_AllChecker();
    allChecker.activeOnProfile();
  } else if(T_Util.isProfileViewPage(parser)) {
    var allChecker = new SD_AllChecker();
    allChecker.activeOnProfileView();
  }
  if(T_Util.isFieldEditPage(parser)) {
    var allChecker = new SD_AllChecker();
    allChecker.activeOnFieldPer();
  }
  // DebugLog viewer
  if(T_Util.isDebugLogDetailPage(parser)) {
    var conn = new SD_OAuthConnect();
    conn.checkStoreMetaData(function(success){
      if(!success) return;
      T_LogViewer = new SD_DebugLogger({ conn : conn });
      T_LogViewer.active();
    });
  }
  // Field Mass Edit
  if(T_Util.isFieldListPage(parser)) {
    var conn = new SD_OAuthConnect();
    conn.checkStoreMetaData(function(success){
      if(!success) return;
      T_FieldMassEdit = new SD_FieldMassEdit({ conn : conn });
      T_FieldMassEdit.activeOnClassic();
    });
  }
});

/*******************************************************************************
 * Community with Tabs
 ******************************************************************************/
var T_callFromBackground = function(data) {
  //console.log('>>>> T_callFromBackground ', data);
  if(data.mode == 'SD_openapp'){
    var parser = T_Util.getParser(window.location.href);
    if(!T_Util.isSfdcDomain(parser.hostname) && !T_Util.isLightningDomain(parser)) return;
    if(!T_App) {
      T_App = new SD_App();
    }
    T_App.run();
    return;
  }
  if(data.mode == 'SD_reportIssue'){
    T_App.reportIssue({message : 'Issue title'}, function(data){
      // TODO
      return sendResponse(data);
    });
    return true;
  }
  if(data.mode == 'SD_sfdcoauthcallback'){
    //console.log('>>>> SD_sfdcoauthcallback', data);
    if(T_App && T_App.authPending){
      var conn = new SD_OAuthConnect(data.token);
      T_App.userInfo = data.token;
      T_App.setConn(conn); 
      var soql = 'SELECT Id, Name, LanguageLocaleKey, TimeZoneSidKey FROM User WHERE Id = \''+T_App.userInfo.userId + '\' LIMIT 1';
      conn.query(soql, function(err, res) {
        var record = res.records[0];
        data.token['language'] = record.LanguageLocaleKey;
        data.token['timezone'] = record.TimeZoneSidKey;
        //console.log('>>>> data.token', data);
        conn.storeLocalOAuthToken(data.token, function(success){
          if(data.token.language && T_FieldMapping.hasOwnProperty(data.token.language)) {
            // Fix language undefined in Lightning
            T_FieldLabelMap = T_FieldMapping[data.token.language];
          }
          if(typeof T_App.authPending == 'function') {
            var tokenInfo = data.token;
            tokenInfo['mode'] = 'oauth';
            T_App.authPending(tokenInfo);
            T_App.authPending = null;
          }
          return true;
        });
      });
      return true;
      //console.log('>>> callback ', data, conn.conn)
    }
  }
}