/*******************************************************************************
 * SD_Logger Class
 ******************************************************************************/
var SD_Logger = function(options){
  options = options || {};
  this.lines = [];
  this.debugmode = options.debugmode || false;
}
/**
 * @param  user info
 */
SD_Logger.prototype.setUserInfo = function(userInfo){
  var info = Object.assign({}, userInfo);
  if(T_Util.isBlank(info)) return this.log('SESSION', info);
  //encrypt security info
  info['orgId'] = (T_Util.isBlank(info.orgId)) ? info.orgId : 'ENCRYPTED';
  info['sessionId'] = (T_Util.isBlank(info.sessionId)) ? info.sessionId : 'ENCRYPTED';
  info['userName'] = (T_Util.isBlank(info.userName)) ? info.userName : 'ENCRYPTED';
  info['userId'] = (T_Util.isBlank(info.userId)) ? info.userId : 'ENCRYPTED';
  if(T_Util.isNotBlank(info.accessToken)) {
    info['accessToken'] = 'ENCRYPTED';
  }
  if(T_Util.isNotBlank(info.refreshToken)) {
    info['refreshToken'] = 'ENCRYPTED';
  }
  this.log('SESSION', info);
}
/**
 * @param  Any Type args 
 */
SD_Logger.prototype.log = function(options){
  var logs = [];
  logs.push('[' + moment().format("YYYY-MM-DD HH:mm") + ']');
  if(this.debugmode) console.log(logs, arguments);
  this._pushLog(logs, arguments);
}
/**
 * @param  Any Type args 
 */
SD_Logger.prototype.error = function(options){
  var logs = [];
  logs.push('[' + moment().format("YYYY-MM-DD HH:mm") + ']');
  logs.push('[ERROR]');
  for(var i = 0; i < arguments.length; i++) {
    var arg = arguments[i];
    if(typeof arg == 'string') {
      arg = (i==0) ? arg : ', ' + arg;
      logs.push(arg);
    } else {
      var message = (arg.hasOwnProperty('stack')) ? arg.stack : arg + '';
      message = (i==0) ? message : ', ' + message;
      logs.push(message);
    }
  }
  if(this.debugmode) console.error(logs);
  this.lines.push(logs.join(' '));
}

/**
 * Convert all log to line
 * @return String
 */
SD_Logger.prototype.getAllLog = function(){
  return this.lines.join('\n');
} 
/**
parser.href = "http://example.com:3000/pathname/?search=test#hash";
parser.protocol; // => "http:"
parser.host;     // => "example.com:3000"
parser.hostname; // => "example.com"
parser.port;     // => "3000"
parser.pathname; // => "/pathname/"
parser.hash;     // => "#hash"
parser.search;   // => "?search=test"
 */
/**
 * Check if github new issue page
 * @param  Object  parser 
 * @return Boolean
 */
SD_Logger.prototype.isGithubIssue = function(parser){
  if(parser.hostname == 'github.com' && 
    parser.pathname == '/xgeek-net/salesforce-mass-editor/issues/new'){
    return true;
  }
  return false;
} 
/**
 * pre-fill issue body
 * @param  Object  parser 
 * @return Boolean
 */
SD_Logger.prototype.prefillIssue = function(){
  var code = this._getParam('code');
  if(T_Util.isBlank(code)) return;
  chrome.storage.local.get(code, function (data) {
    if(chrome.runtime.lastError){
      return;
    }
    if(!T_Util.isBlank(data) && data.hasOwnProperty(code)) {
      var comment = '`You can also send the following log data to admin@xgeek.net, if you don\'t wanna public any information on Github`';
      comment += '\n──────────────────────────────────────────────\n' + data[code];
      jQuery('#issue_body').val(comment);
      chrome.storage.local.remove(code);
    }
  });
}
/**
 * Push log to cache
 * @param Array logs
 * @param Array args arguments
 */
SD_Logger.prototype._pushLog = function(logs, args){
  for(var i = 0; i < args.length; i++) {
    var arg = args[i];
    if(typeof arg == 'string') {
      arg = (i==0) ? arg : ', ' + arg;
      logs.push(arg);
    } else {
      arg = (i==0) ? JSON.stringify(arg) : ', ' + JSON.stringify(arg);
      logs.push(arg);
    }
  }
  this.lines.push(logs.join(' '));
}
/**
 * get GET parameter from url
 * @param  String name parameter name
 * @param  String url  url or blank
 * @return String parameter value
 */
SD_Logger.prototype._getParam = function(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
}
