/*******************************************************************************
 * SD_Util Class
 ******************************************************************************/
var SD_Util = function(){

}
SD_Util.prototype.isBlank = function(obj){
  if( typeof obj === 'undefined' || obj === null ) {
      return true;
  }
  if( typeof obj === 'string' && (obj === "undefined" || obj === "null" || obj === '') ) {
      return true;
  }
  return false;
}
SD_Util.prototype.isNotBlank = function(obj){
  return !this.isBlank(obj);
}
SD_Util.prototype.isNumber = function(num){
  var reg = new RegExp('^[0-9]+$');
  return reg.test(num);
}
SD_Util.prototype.isTrue = function(val){
  return (val == 'true' || val == true);
}
SD_Util.prototype.getNumber = function(str){
  if(this.isBlank(str)) return null;
  var matches = str.match(/([1-9]\d*|0)(\.\d+)?/g);
  return this.isBlank(matches) ? null : matches[0];
}
// Get Excel column from number, 0 → A
SD_Util.prototype.getColumn = function(num){
  var columnName = '';
  var modulo;
  while (num > 0){
    modulo = (num - 1) % 26;
    columnName = String.fromCharCode(65 + modulo) + columnName;
    num = parseInt((num - modulo) / 26);
  } 
  return columnName;
}
// 'abcde' → 5, 'あいうえお' → 10
SD_Util.prototype.getBytes = function(txt) {
  var length = 0;
  for (var i = 0; i < txt.length; i++) {
    var c = txt.charCodeAt(i);
    if ((c >= 0x0 && c < 0x81) || (c === 0xf8f0) || (c >= 0xff61 && c < 0xffa0) || (c >= 0xf8f1 && c < 0xf8f4)) {
      length += 1;
    } else {
      length += 2;
    }
  }
  return length;
}
SD_Util.prototype.escapeString = function(text){
  return String(text)
    .replace(/\r?\n/g,'\\n')
    .replace(/\r?\t/g,'\\t')
    .replace(/'/g, '\'')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/&/g, '&amp;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#39;');
}
SD_Util.prototype.punescape = function(text){
  return String(text)
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&')
    .replace(/&quot;/g, '"')
    .replace(/&#39;/g, "\'");
}

SD_Util.prototype.escHtml = function(value){
  value = value || '';
  return value.replace(/&/g, "&amp;").replace(/>/g, "&gt;").replace(/</g, "&lt;").replace(/"/g, "&quot;");
}
SD_Util.prototype.getSpaceByNum = function(cnt){
  var str = '';
  for(var i = 0; i < cnt; i++){
    str += ' ';
  }
  return str;
}
// property大文字にする
SD_Util.prototype.upperProperty = function(obj){
  var map = {};
  // 忽略数组
  if(Array.isArray(obj)) return obj;
  var keys = Object.keys(obj);
  for(var i=0; i<keys.length; i++) {
    var key = keys[i];
    if(typeof obj[key] == 'object' && !this.isBlank(obj[key])) {
      map[key.toUpperCase()] = this.upperProperty(obj[key]);
    } else {
      map[key.toUpperCase()] = obj[key];
    }
  }
  return map;
}

//获取列表页的三位ID
SD_Util.prototype.getIdPreFixFromUri = function(uri){
  if(this.isBlank(uri)) return null;
  var parser = this.getParser(uri);
  if(this.isBlank(parser.pathname)) return null;
  if(parser.pathname.length > 8) return null;
  var marches = parser.pathname.match(/\/[a-zA-Z0-9]{3}/);
  if(this.isBlank(marches) || marches.length == 0) return null;
  var oId = marches[0].replace(new RegExp('\/', 'g'), '');
  return oId;
}
//获取详细页的15位ID
SD_Util.prototype.getObjectIdFromUri = function(uri){
  if(this.isBlank(uri)) return null;
  var parser = this.getParser(uri);
  if(this.isBlank(parser.pathname)) return null;
  var marches = parser.pathname.match(/\/[a-zA-Z0-9]{15,18}/);
  if(this.isBlank(marches) || marches.length == 0) return null;
  var oId = marches[0].replace(new RegExp('\/', 'g'), '');
  return oId;
}
//获取关联列表Object名
SD_Util.prototype.getRelatedObjectName = function(relatedName){
  var regexp = /^Related([a-zA-Z0-9]+)List$/g;
  var matches = regexp.exec(relatedName);
  if(this.isBlank(matches) || matches.length < 2) return null;
  return matches[1];
}
/**
parser.href = "http://example.com:3000/pathname/?search=test#hash";
parser.protocol; // => "http:"
parser.host;     // => "example.com:3000"
parser.hostname; // => "example.com"
parser.port;     // => "3000"
parser.pathname; // => "/pathname/"
parser.hash;     // => "#hash"
parser.search;   // => "?search=test"
 */
SD_Util.prototype.getParser = function(url){
  var parser = document.createElement('a');
  parser.href = url;
  return parser;
}

// 验证日期是否合法
SD_Util.prototype.isValidDate = function(dateString) {
  var bits = dateString.split('/');
  if(bits.length != 3) return false;
  var d = new Date(bits[0], bits[1] - 1, bits[2]);
  return d.getFullYear() == bits[0] && (d.getMonth() + 1) == bits[1] && d.getDate() == Number(bits[2]);
}
// 验证日期时间是否合法
SD_Util.prototype.isValidDateTime = function(datetimeString) {
  var time = Date.parse(datetimeString);
  if(Number.isNaN(time)) return false;
  return true;
}
// 是否Salesforce.com Domain
SD_Util.prototype.isSfdcDomain = function(domain) {
  if(this.isBlank(domain)) return false;
  return (domain.indexOf('.salesforce.com') >= 0 || domain.indexOf('.cloudforce.com') >= 0 || domain.indexOf('.force.com') >= 0);
}
// 是否Salesforce.com Lightning Domain
// https://domain/one/one.app/#/sObject/Account/home
// https://domain/lightning/o/Account/home
SD_Util.prototype.isLightningDomain = function(parser) {
  if(typeof parser == 'undefined' || typeof parser.hostname == 'undefined') return false;
  return (parser.hostname.indexOf('.lightning.force.com') >= 0);
}
// 是否Salesforce.com Community Domain
SD_Util.prototype.isCommunityDomain = function(parser) {
  if(this.isBlank(parser)) return false;
  if(this.isLightningDomain(parser)) return false;
  return (parser.hostname.indexOf('.force.com') >= 0);
}
// 是否Salesforce.com Community Napili Template
SD_Util.prototype.isCommunityNapili = function(parser) {
  if(this.isBlank(parser)) return false;
  return (this.isCommunityDomain(parser) && parser.pathname.indexOf('/recordlist/') >= 0);
}

SD_Util.prototype.isOAuthCallback = function(parser){
  var extensionId = T_OAuthConfig.extensionId || chrome.runtime.id;
  var paramId = this.getParam('source');
  if((parser.hostname == 'login.salesforce.com' || parser.hostname == 'test.salesforce.com') 
    && parser.pathname == '/services/oauth2/success' && extensionId == paramId){
    return true;
  }
  return false;
}

