// GA event
SD_Util.prototype.gaTrack = function(options) {
  chrome.runtime.sendMessage({
    type:    'SD_GaTrack',
    options: options
  });
}

SD_Util.prototype.hashCode = function(s){
  return s.split("").reduce(function(a,b){a=((a<<5)-a)+b.charCodeAt(0);return a&a},0);              
}

// Open tab
SD_Util.prototype.openTab = function(options) {
  chrome.runtime.sendMessage({
    type:    'opentab',
    options: options
  });
}
/**
 * get GET parameter from url
 * @param  String name parameter name
 * @param  String url  url or blank
 * @return String parameter value
 */
SD_Util.prototype.getParam = function(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
}
// Get uuid
SD_Util.prototype.getUUid = function() {
  var strong = 1000;
  return new Date().getTime().toString(16)  + Math.floor(strong*Math.random()).toString(16);
}
// Get Random String
SD_Util.prototype.getRandom = function(length) {
  var l = length || 8;
  var c = "abcdefghijklmnopqrstuvwxyz0123456789";
  var cl = c.length;
  var r = "";
  for(var i=0; i<l; i++){
    r += c[Math.floor(Math.random()*cl)];
  }
  return r;
}
SD_Util.prototype.replaceLast = function(text, what, to){
  return String(text).replace(new RegExp(what + '$'), to);
}
// Replace first match word
SD_Util.prototype.replaceWordFirst = function(text, what, to){
  return text.replace(new RegExp(what, 'i'), to);
}
// Replace last match word
SD_Util.prototype.replaceWordLast = function(text, what, to){
  var n = text.toLowerCase().lastIndexOf(what.toLowerCase());
  var pat = new RegExp(what, 'i');
  return text.slice(0, n) + text.slice(n).replace(pat, to);
}
// Replace break line
SD_Util.prototype.replaceBreakLine = function(text){
  return text.replace(/\r?\n/g, '');
}
// Concat two array without dumplicate
SD_Util.prototype.concatWithoutDuplicate = function(arr1, arr2){
  for(var i = 0; i < arr2.length; i++) {
    if(arr1.indexOf(arr2[i]) >= 0) continue;
    arr1.push(arr2[i]);
  }
  return arr1;
}
// Clear record attributes property
SD_Util.prototype.clearAttributes = function(records){
  if(!records || !Array.isArray(records)) return records;
  for(var i = 0; i < records.length; i++) {
    if(records[i].hasOwnProperty('attributes')) delete records[i].attributes;
  }
  return records;
}

// Base64 encode JSON string 
SD_Util.prototype.base64Encode = function(obj){
  var jsonStr = JSON.stringify(obj);
  return btoa(encodeURIComponent(jsonStr))
}
// Base64 decode JSON string 
SD_Util.prototype.base64Decode = function(txt){
  return decodeURIComponent(atob(txt));
}

// Splite array with 10 items
SD_Util.prototype.splitQueue = function(targets, size){
  var exceededLimit = size || 10;
  if(targets.length > exceededLimit) {
    var pages = [];
    for(var i = 0; i < targets.length; i++) {
      if(i % exceededLimit == 0) {
        pages.push([]);
      }
      pages[(pages.length - 1)].push(targets[i]);
    }
    targets = pages;
  } else {
    targets = [targets];
  }
  return targets;
}
// Convert column index into corresponding column letter
SD_Util.prototype.columnToLetter = function(column){
  var temp, letter = '';
  while (column > 0) {
    temp = (column - 1) % 26;
    letter = String.fromCharCode(temp + 65) + letter;
    column = (column - temp - 1) / 26;
  }
  return letter;
}
// Replace color text
SD_Util.prototype.colorText = function(str, startIndex, length){
  var position = startIndex + length;
  var text = [str.slice(0, position), '</font>', str.slice(position)].join('');
  text = [text.slice(0, startIndex), '<font>', text.slice(startIndex)].join('');
  return text;
}
// Calculate usage percent 
SD_Util.prototype.calcUsage = function(usedCount, total){
  if(total == 0 || usedCount == 0) return 0;
  var percent = (usedCount / total) * 100;
  if(percent < 1) return Math.ceil(percent * 10) / 10; // 2桁切り上げ
  return Math.floor(percent);
}

/*-----------------------------------------------------------------*
 * Global Functions
 *-----------------------------------------------------------------*/
String.prototype.rtrim = function () {
  return this.replace(/((\s*\S+)*)\s*/, "$1");
}
String.prototype.ltrim = function () {
  return this.replace(/\s*((\S+\s*)*)/, "$1");
}
/** 清楚空格，换行跟tag */
String.prototype.clearSpace = function (){  
  if( typeof this === 'string' && (this === "undefined" || this === "null" || this === '') ) {
    return this;
  }
  return this.replace(/\n/g,'').replace(/\t/g,'').replace(/ /g,'');
}  
String.prototype.insertAt = function (index, string) {
  if (index > 0)
    return this.substring(0, index) + string + this.substring(index, this.length);
  else
    return string + this;
};
/** 开始载入 */
$.fn.startLoading = function( option ) {
  //var options = option || {};
  var $btn = $(this);
  // Button
  var textMarginCls = '';
  if($btn.text().clearSpace().length > 0){
    textMarginCls = ' mr5';
  }
  var loadingIcon = '<i class="fa fa-spinner fa-pulse loading-icon'+textMarginCls+'"></i>';
  if($btn.find('i').length > 0){
    $btn.find('i').addClass('hide');
  }
  $btn.prepend(loadingIcon);
  $btn.addClass('disabled').attr('disabled', 'disabled');
};
/** 停止载入 */
$.fn.stopLoading = function() {
  var $btn = $(this);
  // Button
  $btn.removeClass('disabled').removeAttr('disabled');
  $btn.find('.loading-icon').remove();
  if($btn.find('i').length > 0){
    $btn.find('i').removeClass('hide');
  }
};
$.fn.isLoading = function() {
  var $btn = $(this);
  if($btn.find('.loading-icon').length > 0) return true;
  return false;
}