String.prototype.insertAt = function (index, string) {
  if (index > 0)
    return this.substring(0, index) + string + this.substring(index, this.length);
  else
    return string + this;
};
/*******************************************************************************
 * DebugLog Class
 ******************************************************************************/ 
var SD_DebugLogger = function(options){
  options = options || {};
  this.conn = options.conn;
  
  this.nameMeta = options.conn.metadataNameMap || {};
  this.lineTags = {};

  var metadata = options.conn.metadata || [];
  this.prefixMeta = {};
  for(var i = 0; i < metadata.length; i++) {
    var meta = metadata[i];
    this.prefixMeta[meta.keyPrefix] = meta;
  }
}

/**
 * Active feature
 * @param  options : { }
 */
SD_DebugLogger.prototype.active = function(options){
  options = options || {};
  var _this = this;
  //if(jQuery('#SD_Debug_Panel').length == 0) 
  _this.lineTags = {};
  _this.initDebugPanelHtml();
  if(options.callback) options.callback();
}
/**
 * 转换成Event Array
 * {
 *   USER_DEBUG : 'apexcode'
 * }
 */
SD_DebugLogger.prototype.getEventCategories = function(options){
  var master = this.getEventMaster();
  var categories = Object.keys(master);
  var eventCategories = {};
  for(var i = 0; i < categories.length; i++) {
    var ckey = categories[i];
    var category = master[ckey];
    var events = category.events;
    for(var e = 0; e < events.length; e++){
      eventCategories[events[e]] = ckey;
    }
    //eventList = eventList.concat(events);
  }
  return eventCategories;
}

// Panel初始化
SD_DebugLogger.prototype.initDebugPanelHtml = function(options){
  var _this = this;
  var eventCategories = _this.getEventCategories();
  var errorEvents = _this.getErrorEvents();
  //console.log('>>>>> metadata ' , _this.prefixMeta);
  var $debugLog = jQuery('pre.codeBlock').first();
  var logLines = $debugLog.text().split('\n');
  _this.lineTags = _this.formatLineTags({ eventCategories : eventCategories, lines : logLines });

  var debugLogHtml = '<pre class="codeBlock" id="SD_Debug_Lines">';
  for(var i=0; i<logLines.length; i++){
    var line = logLines[i];
    //{ line : 'xxxx', preline : 'xxxx', content : 'xxxx', tag : 'USER_DEBUG', category : 'apexcode', level : 'INFO',  }
    var lineInfo = _this.analyticLineInfo({ eventCategories : eventCategories, line : line });
    
    var replacedLine = _this.parseJsonLine({ line : lineInfo.content });
    if(replacedLine != false) {
      lineInfo.content = replacedLine;
    } else {
      // Lookup ID替换
      lineInfo.content = _this.replaceLookupIds({ line : lineInfo.content, metadata : _this.prefixMeta });
    }
    // DEBUG 颜色处理
    if(lineInfo.tag == 'USER_DEBUG'){
      lineInfo.content = _this.styleDebugLevel({ line : lineInfo.content, level : lineInfo.level });
    }
    if(errorEvents.indexOf(lineInfo.tag) >= 0) {
      lineInfo.content = '<span class="code-inner-content-error">' + lineInfo.content + '</span>';
    }
    // 单行Line HTML
    lineInfo.content = '<span class="code-inner-content">' + lineInfo.content + '</span>';
    var lineNumber = '<span class="line-number">' + (i+1) + '</span>';
    var codeInner = '<span class="code-inner" data-tag="'+lineInfo.tag+'" data-category="'+lineInfo.category+'">' + lineInfo.preline + lineInfo.content + '</span>';
    debugLogHtml += '<span id="SD_Debug_Log_Line_'+i+'">' + lineNumber + codeInner + '</span>';
  }
  debugLogHtml += '</pre>';
  $debugLog.hide().after(debugLogHtml);

  // Style Sheet
  var css = _this.getStylesheet();

  jQuery('<style>').prop('type', 'text/css').prop('href', '//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css').appendTo('head');
  jQuery('<style>').prop('type', 'text/css').html(css).appendTo('head');

  // Event Listener
  _this.setEventListener();

  // Handle Hash NO
  var parser = T_Util.getParser(window.location.href);
  if(T_Util.isNotBlank(parser.hash)) {
    var lineNo = parser.hash.replace('#', '');
    if(!T_Util.isNumber(lineNo)) return;
    var $line = jQuery('#SD_Debug_Log_Line_' + (parseInt(lineNo) - 1));
    $line.find('.code-inner').addClass('highlight');
    _this.scrollTo($line);
  }
}

