/**
 * Panel HTML
 * @param  options : {}
 */
SD_DebugLogger.prototype.getPanelHtml = function(options){
  options = options || {};
  var _this = this;
  var html = '<div id="SD_Debug_Panel" class="close">';
  html += '<div class="panel-title"><h2><a href="https://www.xgeek.net/salesforce/tesforce-for-google-chrome/" target="_blank">Debug Log Viewer</a></h2>';
  html += '<a href="javascript:void(0);" class="close pull-right"><i class="fa fa-expand"></i></a></div>';
  html += '<div class="panel-head"><label><input type="checkbox" class="checkbox checkbox-all" checked><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;All</label>';
  html += '&nbsp;&nbsp;&nbsp;&nbsp;<label><input type="checkbox" class="checkbox checkbox-debug-only" data-tag="USER_DEBUG" data-category="apexcode"><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;Debug Only</label>';
  html += '&nbsp;&nbsp;&nbsp;&nbsp;<label><input type="checkbox" class="checkbox checkbox-query-only" data-tag="SOQL_EXECUTE_BEGIN" data-category="db"><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;Query Only</label>';
  html += '</div><ul class="panel-category">';
  var master = _this.getEventMaster();
  var categories = Object.keys(master);
  var debugTags = Object.keys(_this.lineTags);
  for(var i = 0; i < categories.length; i++) {
    var ckey = categories[i];
    var category = master[ckey];
    var events = category.events;
    var open = (i == 0) ? 'open' : '';
    var show = (i == 0) ? 'display: block;' : '';
    cHtml = '<li><div class="panel-header '+open+'"><label><input type="checkbox" class="checkbox checkbox-category" data-category="'+ckey+'" checked><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;</label>';
    cHtml += '<a href="javascript:void(0);" class="category-menu" data-category="'+ckey+'">'+category.label+'</a><a href="javascript:void(0);" class="collapse pull-right"><i class="fa fa-angle-down"></i></a></div><ul class="panel-event" style="'+show+'">';
    var existEvent = false;
    for(var k = 0; k < events.length; k++) {
      if(debugTags.indexOf(events[k]) < 0) continue;
      cHtml += '<li><label><input type="checkbox" class="checkbox checkbox-event" data-tag="'+events[k]+'" checked><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;</label>';
      cHtml += '<a href="javascript:void(0);" class="event-menu" data-tag="'+events[k]+'">'+events[k]+'</a></li>';
      existEvent = true;
    }
    cHtml += '</ul></li>';
    html += (existEvent) ? cHtml : '';
  }
  // Other log lines
  html += '<li><div class="panel-header"><label><input type="checkbox" class="checkbox checkbox-category" data-category="other" checked><span class="check-icon"><i class="fa fa-check"></i></span>&nbsp;</label>';
  html += '<a href="javascript:void(0);" class="category-menu" data-category="other">Other</a></div></li>';
    
  html += '</ul></div>';
  return html;
}

// Event Handler
SD_DebugLogger.prototype.highlight = function(options){
  var self = this;
  self.clearHighlight();
  var $lines = jQuery('#SD_Debug_Lines');
  var type = T_Util.isBlank(options.tag) ? 'category' : 'tag';
  var target = T_Util.isBlank(options.tag) ? options.category : options.tag;
  var line = T_Util.isBlank(options.line) ? 1 : parseInt(options.line);
  var $codes = $lines.find('.code-inner[data-'+type+'="'+target+'"]');
  if($codes.length > 0) {
    var index = line % $codes.length;
    index = (index === 0) ? $codes.length : index;  //0 → last index
    $codes.addClass('highlight');
    self.scrollTo($codes.children().eq(index - 1));
  }
}
// {categories : ['ApexCode'], checked : true}
// {tags : ['USER_DEBUG'], checked : true}
SD_DebugLogger.prototype.taggle = function(options){
  var $lines = jQuery('#SD_Debug_Lines');
  var type = T_Util.isBlank(options.tags) ? 'category' : 'tag';
  var targets = T_Util.isBlank(options.tags) ? options.categories : options.tags;
  for(var i = 0; i < targets.length; i++) {
    var target = targets[i];
    var $codes = $lines.find('.code-inner[data-'+type+'="'+target+'"]');
    if(options.checked) {
      $codes.parent().removeClass('hide');
    } else {
      $codes.parent().addClass('hide');
    }
  }
}

// Event Listener
SD_DebugLogger.prototype.setEventListener = function(options){
  options = options || {};
  var _this = this;
  var $lines = jQuery('#SD_Debug_Lines');
  /** SObject Viewer */
  $lines.find('.debug-sobject-viewer-btn')
  .webuiPopover({
  title: 'SObjects', 
  content: function(){ 
    var json = jQuery(this).data('json');
    if(typeof json === 'string') {
      json = JSON.parse(json);
    }
    var html = _this.getSObjectViewerHtml({ json : json });
    return html;
  }, 
  onShow: function($element) {
    // Set Title
    var title = $element.find('input[name="title"]').first().val();
    $element.find('h3.webui-popover-title').html(title);

    $element.find('.collapse').click(function(ev){
      var $menu = jQuery(this).closest('.panel-header');
      var $panel = jQuery(this).closest('.panel-header').siblings('.panel-fields').first();
      if($menu.hasClass('open')) {
        // Close
        $menu.removeClass('open');
        $panel.hide();
      } else {
        $menu.addClass('open');
        $panel.show();
      }
      ev.stopPropagation();
    });

    // TODO cache field label
    var orgWid = $element.width();
    var orgLeft = $element.offset().left;
    var sobjectName = $element.find('input[name="sobject"]').first().val();
    if(!_this.nameMeta.hasOwnProperty(sobjectName)) return;
    _this.conn.describe(sobjectName, function(err, describe) {
      if (err) { return; }
      var fileNameMap = {};
      for(var i = 0; i < describe.fields.length; i++) {
        var f = describe.fields[i];
        fileNameMap[f.name.toUpperCase()] = f.label;
      }
      $element.find('td.field').each(function(){
        var $td = jQuery(this);
        var fieldName = $td.attr('data-field');
        if(fileNameMap.hasOwnProperty(fieldName.toUpperCase())) {
          var label = fileNameMap[fieldName.toUpperCase()];
          $td.html(label + '&nbsp;<span class="small">('+fieldName+')</span>');
        }
      });
      var newWid = $element.width();
      // reject postion for new size
      $element.css('left', (orgLeft - newWid + orgWid + 10) + 'px' );
    });
  },
  onHide: function($element) {
    $element.find('.collapse').off();
  }, placement:'left-bottom', multi:false, closeable:true, trigger:'click'});

  
}
/**
 * Panel HTML
 * @param  options : {}
 */
SD_DebugLogger.prototype.getSObjectViewerHtml = function(options){
  options = options || {};
  var _this = this;
  var sobjects = options.json;
  var nameMeta = _this.nameMeta;
  var appPath = 'chrome-extension://' + chrome.runtime.id + '/';
  var html = '';
  html += '<ul class="panel-category">';
  var vTitle, sobjectName; // Viewer Title
  var oneType = true;
  for(var i = 0; i < sobjects.length; i++) {
    var sobject = sobjects[i];
    var fields = sobject.fields;
    var sobjMeta = nameMeta[sobject.sobject]
    if(sobject.hasOwnProperty('NAME')) {
      title = sobject.NAME;
    } else if(sobject.hasOwnProperty('ID')) {
      title = sobject.ID;
    } else {
      title = sobjMeta.label + ' (#' + (i + 1) + ')';
    }
    var open = (i == 0) ? 'open' : '';
    var show = (i == 0) ? 'display: block;' : '';
    cHtml = '<li><div class="panel-header '+open+'">';
    cHtml += '<a href="javascript:void(0);" class="collapse">'+title+'</a><a href="javascript:void(0);" class="collapse pull-right">';
    cHtml += '<img src="'+appPath+'dist/image/fa-angle-down.png" width="11" class="down" /><img src="'+appPath+'dist/image/fa-angle-up.png" class="up" width="11" /></a></div><div class="panel-fields" style="'+show+'">';
    cHtml += '<table class="table table-striped">';
    for(var f in fields) {
      var value = fields[f];
      var isObjId = _this.isObjectId({ value : value, metadata : _this.prefixMeta });
      if(isObjId) {
        value = '<a href="/'+value+'" target="_blank">'+value+'</a>';
      }
      cHtml += '<tr><td class="field" data-field="'+f+'">'+f + '</td><td>' + value +'</td></tr>';
    }
    cHtml += '</table></div></li>';
    html += cHtml;

    var titleLabel = sobjMeta.label + ' (' + sobjMeta.name + ')';
    sobjectName = sobjMeta.name;
    if(i == 0) {
      vTitle = titleLabel;
    }
    if(vTitle != titleLabel) {
      oneType = false;
    }
  }
  vTitle = (oneType) ? vTitle : 'SObjects';
  var sobjectName = (oneType) ? sobjectName : '';
  html += '<input type="hidden" name="title" value="'+vTitle+'" />';
  html += '<input type="hidden" name="sobject" value="'+sobjectName+'" />';
  html += '</ul>';
  //</div>
  return html;
}

// Clear Highlight
