/*******************************************************************************
 * SD_OAuthConnect Class
 ******************************************************************************/
var SD_OAuthConnect = function(options){
  options = options || {};
  this.authInfo = null;
  this.authed = false;
  // Classic user info
  this.userInfo = this.getUserInfo();
  this.cookieObj = this.getCookieSession();
  //console.log('cookieObj', this.cookieObj);
  this.metadata = null;
  this.metadataNameMap = null;
  this.metadataPrefixMap = null;
  this.filedsMetadata = null;

  // Lightning && Community , clear session id
  var parser = T_Util.getParser(window.location.href);
  if(T_Util.isLightningDomain(parser) || T_Util.isCommunityDomain(parser)) {
    if(T_Util.isNotBlank(this.cookieObj.sessionId)) this.cookieObj['sessionId'] = null;
  }

  if(options.accessToken && options.refreshToken) {
    // Callback from OAuth2
    this.conn = this.getConn(options);
    this.authInfo = options;
    if(options.orgId && options.userId) {
      this.userInfo = options;
      return;
    }
  }
  if(T_Util.isNotBlank(this.cookieObj.sessionId) && 
    T_Util.isNotBlank(this.userInfo) && 
    T_Util.isBlank(this.conn)) {
    this.conn = this.getConn(this.userInfo);
  }
}
// Data local storage key
SD_OAuthConnect.prototype.getStoreKey = function(key) {
  var self = this;
  if(T_Util.isBlank(self.userInfo.sessionId) && 
    T_Util.isNotBlank(self.cookieObj.clientId)) {
    // Lightning Or Community
    var clienId = self.cookieObj.clientId;
    return clienId + '_' + key;
  }
  var orgId = (T_Util.isBlank(self.userInfo.orgId)) ? null : self.userInfo.orgId;
  var userId = (T_Util.isBlank(self.userInfo.userId)) ? null : self.userInfo.userId;
  if(T_Util.isBlank(orgId) || T_Util.isBlank(userId)) {
    return null;
  }
  orgId = orgId.substring(0, 15);
  userId = userId.substring(0, 15);
  var clientId = userId.substring(4, 15) + orgId.substring(4, 15);
  return clientId + '_' + key;
}
// init jsforce connection
// options : { instanceUrl : 'xxx', accessToken : 'xxx', refreshToken : 'xxx'}
// options : { sessionId : 'xxx', salesforceURL : 'xxx' }
SD_OAuthConnect.prototype.getConn = function(options) {
  options = options || {};
  // Classic
  if(options.sessionId && options.salesforceURL) {
    var conn = new jsforce.Connection({
      version : SF_API_VERSION,
      serverUrl : options.salesforceURL,
      sessionId : options.sessionId
    });
    //console.log('>>> classic conn ', conn);
    // OAuth 認証有効にする
    this.authed = true;
    return conn;
  }

  // OAuth( Lightning / Classic Community )
  var params = this.getConnParams(options);
  var oauth2 = this.getOAuthClient(params);
  var conn = new jsforce.Connection({
    version : SF_API_VERSION,
    oauth2 : oauth2,
    instanceUrl : options.instanceUrl,
    accessToken : options.accessToken,
    refreshToken : options.refreshToken
  });
  //console.log('>>>> getConn', conn);
  // OAuth 認証有効にする
  this.authed = true;
  return conn;
}
// get OAuth Client
SD_OAuthConnect.prototype.getOAuthClient = function(options) {
  options = options || {};
  var type = options.type || 'production';
  var redirectUri = (type == 'production') ? 'https://login.salesforce.com' : 'https://test.salesforce.com';
  var loginUrl = options.loginUrl;  // Community login url
  if(T_Util.isBlank(loginUrl)) {
    loginUrl = redirectUri;
  }
  //console.log('>>>> chrome.extension.getURL("some file name")', chrome.extension.getURL('oauth.html'));
  var oauthRedirectUrl = redirectUri + T_OAuthConfig.redirectPath;
  if(options.tabid) oauthRedirectUrl += '&tabid=' + options.tabid;
  var oauth2 = new jsforce.OAuth2({
    loginUrl : loginUrl,
    clientId : T_OAuthConfig.clientId + '.R',
    clientSecret : T_OAuthConfig.clientSecret,
    redirectUri : oauthRedirectUrl
  });
  return oauth2;
}
// Get params for Jsforce connection
SD_OAuthConnect.prototype.getConnParams = function(options) {
  options = options || {};
  var orgType = 'production';
  var instanceUrl = (T_Util.isNotBlank(options.salesforceURL)) ? options.salesforceURL : options.instanceUrl;
  var parser = T_Util.getParser(instanceUrl);
  var hosts = parser.hostname.split('.');
  for(var i = 0; i < hosts.length; i++) {
    var host = hosts[i];
    if(i == 0 && host.indexOf('--') >= 0) {
      // My Domain Sandbox
      orgType = 'sandbox';
      break;
    }
    if(!host.startsWith('cs')) continue;
    var instNum = host.replace(/cs/, '');
    if(T_Util.isNumber(instNum)) {
      orgType = 'sandbox';
      break;
    }
  }
  var params = {type : orgType};
  if(T_Util.isCommunityDomain(parser)) {
    params['loginUrl'] = 'https://' + parser.hostname;
  }
  return params;
}

// get oauth2 url
SD_OAuthConnect.prototype.getAuthorizationUrl = function(options) {
  options = options || {};
  var params = this.getConnParams(this.userInfo);
  if(options.tabid) params['tabid'] = options.tabid;
  var oauth2 = this.getOAuthClient(params);
  //console.log('>>> oauth2 ', oauth2);-
  var url = oauth2.getAuthorizationUrl({ scope : 'api id refresh_token' });
  return url;
}
/**
 * Request accesstoken to refreshtoken
 * Url : https://login.salesforce.com/services/oauth2/success 
 *   or  https://test.salesforce.com/services/oauth2/success
 */
SD_OAuthConnect.prototype.authorize = function(options, callback) {
  var self = this;
  options = options || {};
  var code = options.code;
  var sfdc_community_url = options.sfdc_community_url;
  var url = window.location.href;
  var parser = T_Util.getParser(url);
  var orgType = (parser.hostname == 'login.salesforce.com') ? 'production' : 'sandbox';
  var params = {type : orgType};
  if(T_Util.isNotBlank(sfdc_community_url)) {
    // Callback for Community Url
    params['loginUrl'] = sfdc_community_url;
  }
  var oauth2 = this.getOAuthClient(params);
  self.conn = new jsforce.Connection({ oauth2 : oauth2 });
  self.tokenInfo = self.conn;
  self.conn.authorize(code, function(err, userInfo) {
    if (err) { 
      alert(err.message); 
      return callback(false); 
    }
    if(T_Util.isBlank(userInfo) || T_Util.isBlank(userInfo.id)) {
      console.error('Authorize ERROR', err, userInfo);
      return callback(false);
    }
    var userid = userInfo.id.substring(0, 15);
    var orgid = userInfo.organizationId.substring(0, 15);
    var data = {
      loginUrl : self.conn.loginUrl,
      accessToken : self.conn.accessToken,
      refreshToken : self.conn.refreshToken,
      instanceUrl : self.conn.instanceUrl,
      orgId : orgid,
      userId : userid
    };
    return callback(data);
    //return self.storeLocalOAuthToken(userInfo, self.conn, callback);
  });
}
// 存储OAuth到本地
SD_OAuthConnect.prototype.storeLocalOAuthToken = function(data, callback) {
  var self = this;
  self.saveStorageData('oauth', data, function(success) {
    if(callback) {
      callback(data);
    }
  });
}
// 清楚过期的OAuth
SD_OAuthConnect.prototype.clearLocalAuthToken = function(userInfo, callback) {
  var self = this;
  //console.log('>>> userinfo ', userInfo);
  self.saveStorageData('oauth', {}, function(success){
    if(callback) return callback(success);
  });
}

/** Check OAuth token info from storage */
SD_OAuthConnect.prototype.checkStoredOAuthToken = function(callback) {
  var self = this;
  if(self.authed == true) return callback(true);
  self.getStorageData('oauth', function(data){
    if(data && data.accessToken && data.refreshToken) {
      self.authInfo = data;
      self.conn = self.getConn(data);
      return callback(true);
    } 
    return callback(false);
  });
}
/** Check Metadata */
SD_OAuthConnect.prototype.checkStoreMetaData = function(callback) {
  var self = this;
  
  self.getStorageData('describes', function(data) {
    if(data == false) return callback(false);
    //console.log('>>>> chrome.storage.local.get has data : ' , data);
    if( T_Util.isBlank(data) || T_Util.isBlank(data.data)) {
      //未储存
      return self.restoreMetaData(callback);
    }
    var diff = moment().diff(moment(data.createdtime), 'days');
    if( T_DEBUG_MODE || diff > 0 ) {
      // Restore everyday
      return self.restoreMetaData(callback);
    }

    // 缓存
    self.metadata = data.data;
    self.metadataNameMap = self.converToNameMap(self.metadata);
    self.metadataPrefixMap = self.converToPrefixMap(self.metadata);
    return callback(self.metadata);
  });
}
/**
 * Restore describe objects to local storage
 * @param  {Function} callback 
 * // TODO prefix as key
 */
SD_OAuthConnect.prototype.restoreMetaData = function(callback){
  var self = this;
  if(typeof self.conn == 'undefined') {
    return callback(false);
  }
  self.conn.describeGlobal$.clear();
  var describeGlobalCallback = function(err, res){
    if(err) {
      var isTokenError = self.handleOAuthError(err, function(success){
        if(!success) return callback(false);
        return self.restoreMetaData(callback);
      });
      // Refresh Token
      self.authed = false;
      if(isTokenError) return;
      return callback(false);
    }
    if(T_Util.isBlank(res.sobjects)){
      self.authed = false;
      return callback(false);
    }
    // Authed to ON
    self.authed = true;

    var sobjects = [];
    for(var i = 0; i < res.sobjects.length; i++) {
      var sobj = res.sobjects[i];
      if(T_Util.isBlank(sobj.keyPrefix)) continue;
      if(!sobj.createable && !sobj.updateable && !sobj.queryable) continue;
      sobjects.push(sobj); 
    }
    // Custom Object Id
    var indexMap = {};
    for(var i = 0; i < sobjects.length; i++) {
      if(sobjects[i].custom != true) continue;
      var names = sobjects[i].name.split('__');
      if(names.length == 2) {
        var sobjName = names[0];
        indexMap[sobjName] = i;
      } else if(names.length > 2) {
        // Package prefix
        var sobjName = names[(names.length-2)];
        indexMap[sobjName] = i;
      }
    }
    // Cache object id
    var soql = 'SELECT Id, DeveloperName FROM CustomObject';
    var toolingQueryCallback = function(err, res){
      if(err) {
        var isTokenError = self.handleOAuthError(err, function(success){
          if(!success) return callback(false);
          if(T_App && T_App.isLEX) {
            return T_App.sendCrobRequest({ method : 'tooling.query', params : soql }, toolingQueryCallback);
          }
          return self.conn.tooling.query(soql, toolingQueryCallback);
        });
        // Refresh Token
        self.authed = false;
        if(isTokenError) return;
        return callback(false);
      }
      for(var i = 0; i < res.records.length; i++) {
        var cobj = res.records[i];
        if(indexMap.hasOwnProperty(cobj.DeveloperName) && cobj.Id) {
          var index = indexMap[cobj.DeveloperName];
          sobjects[index]['objectId'] = cobj.Id.substring(0, 15);
        }
      }
      var data = { data : sobjects, createdtime : moment().format() };

      self.metadata = sobjects;
      self.metadataNameMap = self.converToNameMap(self.metadata);
      self.metadataPrefixMap = self.converToPrefixMap(self.metadata);
      self.saveStorageData('describes', data, function(success){
        return callback(self.metadata);
      });
      // Clear Profiles
      self.saveStorageData('profiles', {}, function(success){});
    };
    if(T_App && T_App.isLEX) {
      return T_App.sendCrobRequest({ method : 'tooling.query', params : soql }, toolingQueryCallback);
    }
    self.conn.tooling.query(soql, toolingQueryCallback);
  };
  if(T_App && T_App.isLEX) {
    return T_App.sendCrobRequest({ method : 'describeGlobal' }, describeGlobalCallback);
  }
  self.conn.describeGlobal(describeGlobalCallback);
}

// Get setting json from local
SD_OAuthConnect.prototype.getStorageData = function(key, callback){
  var self = this;
  var dataId = self.getStoreKey(key);
  if(T_Util.isBlank(dataId)) return callback(false);

  chrome.storage.local.get(dataId, function (data) {
    if(chrome.runtime.lastError){
      return callback(false);
    }
    return callback(data[dataId]);
  });
}

// Save data to local
SD_OAuthConnect.prototype.saveStorageData = function(key, data, callback){
  var self = this;
  var dataId = self.getStoreKey(key);
  //console.log('>>>> saveStorageData key : ', dataId);
  if(T_Util.isBlank(dataId)) return callback(false);

  var sData = {};
  sData[dataId] = data;
  chrome.storage.local.set(sData, function () {
    if(chrome.runtime.lastError){
      return callback(false);
    }
    //console.log('>>>> saveStorageData data : ', sData);
    return callback(true);
  });
}

// 以API Name为Key
SD_OAuthConnect.prototype.converToNameMap = function(metadata){
  var data = {};
  for(var key in metadata){
    var meta = metadata[key];
    data[meta.name] = meta;
  }
  return data;
}

// 以Prefix为Key
SD_OAuthConnect.prototype.converToPrefixMap = function(metadata){
  var data = {};
  for(var key in metadata){
    var meta = metadata[key];
    data[meta.keyPrefix] = meta;
  }
  return data;
}

// 以大写API Name为Key
SD_OAuthConnect.prototype.converToFieldNameMap = function(describes){
  var data = {};
  for(var i = 0; i < describes.length; i++){
    var f =  describes[i];
    data[f.name.toUpperCase()] = f;
  }
  return data;
}

// Get editable field list
SD_OAuthConnect.prototype.getFields = function(describe){
  var fields = [];
  for(var i = 0; i < describe.fields.length; i++) {
    var f = describe.fields[i];
    if(f.name.toUpperCase() == 'ID') {
      fields.push(f);
      continue;
    }
    if(!f.createable && !f.updateable) continue;
    fields.push(f);
  }
  return fields;
}
/** 
 * Get userinfo from page html CLASSIC 
 * Precondition of SessionID can be got
 * @return {} || { orgId : '', sessionId : '' }
 */
SD_OAuthConnect.prototype.getUserInfo = function() {
  var url = window.location.href;
  var parser = T_Util.getParser(url);
  if ( !T_Util.isSfdcDomain(parser.hostname)) {
    return null;
  }
  var source = document.documentElement.innerHTML;
  var userInfos = source.match(/UserContext.initialize\((.*?)\);\s+/i);
  //console.log('>>>> userInfos ' + userInfos);
  if(userInfos == null) return {};
  //console.log('>>>> document.cookie ' + JSON.stringify(document.cookie));
  try{
    var cookie = this.getCookieSession();
    //console.log('>>>> cookie ' + JSON.stringify(cookie));
    //var userName = document.cookie.match(/(^|;\s*)login=(.+?);/)[2];
    //var disco = document.cookie.match(/(^|;\s*)disco=(.+?);/)[2];
    //var userId = disco.split(':')[2];
    var obj = JSON.parse(userInfos[1]);
    //console.log('>>>> obj ' + obj.userName + obj.userId);
    return {
      orgId    : cookie.orgId,
      sessionId: cookie.sessionId,
      userName : obj.userName,
      userId   : obj.userId,
      salesforceURL : obj.salesforceURL,
      language : obj.language,
      dateFormat : obj.dateFormat,
      dateTimeFormat : obj.dateTimeFormat
    };
  } catch(e){
    //console.log('>>>> error ' + e);
    return {};
  }
  return userInfos[1];
}
// Get session id from cookie
SD_OAuthConnect.prototype.getCookieSession = function() {
  var dcookie = document.cookie + ';';
  try{
    var orgId = null;
    var orgIds = dcookie.match(/(^|;\s*)oid=(.+?);/);
    if(T_Util.isNotBlank(orgIds && orgIds.length > 1)) {
      var orgId = orgIds[2];
    }
    var sessionId = null;
    var sessions = dcookie.match(/(^|;\s*)sid=(.+?);/);
    if(T_Util.isNotBlank(sessions && sessions.length > 1)) {
      sessionId = sessions[2];
    }
    var clientId = dcookie.match(/(^|;\s*)sid_Client=(.+?);/)[2];
    return {orgId : orgId, sessionId: sessionId, clientId : clientId};
  } catch(e) {
    return {};
  }
}

