// Describe Objects fields metadata
SD_OAuthConnect.prototype.describeSObjects = function(types, callback) {
  var args = {
    '@xmlns' : 'urn:partner.soap.sforce.com',
    '@xmlns:ns1' : 'sobject.partner.soap.sforce.com',
    'ns1:describeSObjects' : types
  };
  if(T_App && T_App.isLEX) {
    return T_App.sendCrobRequest({ method : 'describeSObjects', params : args }, callback);
  }
  return this.conn.soap._invoke("describeSObjects", args, [{}], callback);
}
