SD_OAuthConnect.prototype.handleOAuthError = function(err, callback) {
  var self = this;
  if( (err.errorCode == 'INVALID_SESSION_ID' || err.errorCode == 'sf:INVALID_SESSION_ID'  
      || err.errorCode == 'INVALID_LOGIN' || err.errorCode == 'sf:INVALID_LOGIN' 
      || err.errorCode == 'INVALID_OPERATION_WITH_EXPIRED_PASSWORD')
      || err.name == 'invalid_grant'
    ) {
    // OAuth 認証無効にする
    self.authed = false;
    if(self.authInfo.refreshToken) {
      var refreshToken = self.conn.refreshToken;
      var refreshCallback = function(err, res) {
        if(err) {
          self.clearLocalAuthToken(self.userInfo);
          return callback(false);
        }
        if(T_Util.isBlank(self.userInfo) || T_Util.isBlank(self.userInfo.userId)) {
          if(T_Util.isBlank(res) || T_Util.isBlank(res.id)) {
            self.clearLocalAuthToken(self.userInfo);
            return callback(false);
          }
          // id: "https://test.salesforce.com/id/00D9D0000008bDiUAI/0050o00000WI0DxAAL"
          var marches = res.id.match(/[a-zA-Z0-9]{15,18}/g);
          if(T_Util.isNotBlank(marches) && marches.length == 2) {
            self.userInfo = { userId : marches[1], orgId : marches[0] };
          }
        }
        var userid = self.userInfo.userId.substring(0, 15);
        var orgid = self.userInfo.orgId.substring(0, 15);
        var data = {
          loginUrl : self.conn.loginUrl,
          accessToken : res.access_token || res.accessToken,
          refreshToken : self.conn.refreshToken,
          instanceUrl : self.conn.instanceUrl,
          orgId : orgid,
          userId : userid
        };
        var conn = new SD_OAuthConnect(data);
        T_App.setConn(conn); 

        var _requestLanguageKeyCallback = function(err, res) {
          if(err) {
            self.clearLocalAuthToken(self.userInfo);
            return callback(false);
          }
          var record = res.records[0];
          data['language'] = record.LanguageLocaleKey;
          // Reset Connection
          T_App.userInfo = data;
          return self.storeLocalOAuthToken(data, callback);
        }
        var soql = 'SELECT Id, Name, LanguageLocaleKey FROM User WHERE Id = \''+userid + '\' LIMIT 1';
        self.query(soql, _requestLanguageKeyCallback);
        return true;
      };
      if(T_App && T_App.isLEX) {
        var params = {
          loginUrl : self.conn.oauth2.loginUrl,
          clientId : self.conn.oauth2.clientId,
          clientSecret : self.conn.oauth2.clientSecret,
          redirectUri : self.conn.oauth2.redirectUri,
          refreshToken : refreshToken
        }
        T_App.sendCrobRequest({ method : 'oauth2.refreshToken', params : params }, refreshCallback);
        return true;
      }
      self.conn.oauth2.refreshToken(refreshToken, refreshCallback);
    }
    return true;
  }
  return false;
}