/*******************************************************************************
 * SD_AllChecker Class
 ******************************************************************************/ 
var SD_AllChecker = function(options){
  options = options || {};
}

SD_AllChecker.prototype.getDescrpeLink = function(url){
  var descripe = '<span class="SD_All_Check_small">(';
  descripe += '<a href="'+T_AppConfig.appUrl+'" target="_blank">'+T_AppConfig.appName+'</a>)</span>';
  return descripe;
}

/**
 * 启动SD_AllChecker on Profile参照画面、Editリンク挿入
 */
SD_AllChecker.prototype.activeOnProfileView = function(){
  var $target = jQuery('#bodyTable');
  jQuery('#bodyTable').find('.detailList .dataCol a').each(function(){
    var $link = jQuery(this);
    if($link.get(0).pathname == '/setup/layout/flsdetail.jsp') {
      var ltext = 'Edit';
      if($link.text() == '参照') {
        ltext = '編集';
      }
      var editLink = $link.attr('href').replace('/setup/layout/flsdetail.jsp', '/setup/layout/flsedit.jsp');
      $link.after(' | <a href="'+editLink+'">'+ltext+'</a>');
    }
  });
}

/**
 * 启动SD_AllChecker on Profile
 */
SD_AllChecker.prototype.activeOnProfile = function(){
  var _this = this;
  // Stylesheet
  var css = '.SD_All_Checker { margin-left: 25px; font-size:85%; font-weight: bold; }';
  css += 'body .pbSubheader .SD_All_Check_small{ font-size: 11px; color: grey; font-weight: 400;}';
  css += 'body .pbSubheader .SD_All_Check_small a{ color: grey; }';
  jQuery('<style>').prop('type', 'text/css').html(css).appendTo('head');

  // inject HTML to Profile Edit
  jQuery('#editPage .pbBody .pbSubheader').each(function(){
    var $header = jQuery(this);
    var $subsection = $header.nextUntil('.pbSubheader', '.pbSubsection');
    var result = _this.scanCheckOptions($subsection);
    var chtml = '<span class="SD_All_Checker">';
    if(result.checkbox) {
      // Checkbox时
      var checked = (result.checkbox_default) ? ' checked="checked"' : '';
      chtml += '<label class="all_check_label"><input type="checkbox" value="1" '+checked+' class="SD_All_Check" />&nbsp;Check All&nbsp;&nbsp;</label>';
    }
    if(result.select) {
      // Checkbox时
      var shtml = '<select class="SD_All_Select">';
      shtml += '<option value="">--None--</option>';
      for(var i=0; i<result.options.length; i++){
        var op = result.options[i];
        var selected = (op.value == result.select_default) ? ' selected="selected"' : '';
        shtml += '<option value="'+ op.value +'"'+selected+'>' + op.text + '</option>';
      }
      shtml += '</select>';
      chtml += '<label class="all_select_label">'+shtml+'&nbsp;Select All&nbsp;&nbsp;</label>';
    }
    chtml += '</span>'; // .SD_All_Checker
    if(!result.checkbox && !result.select) return; // 没有Checkbox及Select时不插入
    chtml += _this.getDescrpeLink();
    $header.find('h3').append(chtml);
    // Set Lisener
    $header.find('.SD_All_Check').click(function(){
      var isChecked = jQuery(this).is(':checked');
      $subsection.find(':checkbox:enabled:visible').prop('checked', isChecked);
    });
    $header.find('.SD_All_Select').change(function(){
      var val = jQuery(this).val();
      if(T_Util.isBlank(val)) return;
      $subsection.find('select:enabled:visible').val(val);
    });

  });
  
  // inject HTML to Field permission 

}
