/**
 * 
 * 分析 .pbSubsection中的Checkbox与Select
 */
SD_AllChecker.prototype.scanCheckOptions = function($subsection){
  // Checkbox
  var checkedCnt = 0;
  var $checkboxs = $subsection.find(':checkbox:enabled:visible');
  $checkboxs.each(function(){
    if(jQuery(this).is(':checked')) checkedCnt++;
  });
  var checkCnt = $checkboxs.length;
  var checkDefault = (checkedCnt == checkCnt) ? true : false;

  // Select 
  var $selects = $subsection.find('select:enabled:visible');
  var selectCnt = $selects.length;
  var optionTexts = []; // only text
  var options = []; // value : text
  var rowIndex = 0;
  var hasMultiSelects = true;
  var selectDefault = null;
  $subsection.find('select:enabled:visible').each(function(){
    var selectedVal = jQuery(this).val();
    if(rowIndex == 0) {
       selectDefault = selectedVal;
    }
    if(selectDefault != selectedVal){
      // 有一个不一致就不设置默认值
      selectDefault = null;
    }
    jQuery(this).find('option').each(function(){
      var text = jQuery(this).text();
      var value = jQuery(this).val();
      if(rowIndex == 0) {
        optionTexts.push(text);
        options.push({ value : value, text : text});
      }
      if(optionTexts.indexOf(text) < 0) {
        // 出现不同的options，不考虑All Select
        hasMultiSelects = false;
        return false;
      }
    });
    if(hasMultiSelects == false) return false;
    rowIndex++;
  });
  return {
    'checkbox' : (checkCnt > 1),
    'checkbox_default' : checkDefault,
    'select' : (selectCnt > 1 && hasMultiSelects),
    'select_default' : selectDefault,
    'options' : options
  }
}
/** Multi Checker Popover Listener */
SD_AllChecker.prototype.setMultiCheckerListener = function(){
  var _this = this;
  var $table = jQuery('#mainTable');
  var multiCheckApply = function(targetcls, fields, isChecked){
    var spliteds = fields.split('\n');
    if(spliteds.length == 1) {
      // split with ","
      spliteds = fields.split(',');
    }
    var targetFields = [];
    var notfounds = [];
    var duplicates = [];
    for(var i = 0; i < spliteds.length; i++) {
      var field = spliteds[i].trim();;
      if(field.length == 0) continue;
      targetFields.push(field);
      notfounds.push(field);
    }
    var $subsections = $table.find('.dataRow .'+targetcls);
    
    $subsections.each(function() {
      var $col = jQuery(this);
      var $row = $col.closest('.dataRow');
      var label = $row.find('.dataCell').first().text();
      label = label.trim();
      if(targetFields.indexOf(label) >= 0) {
        // Check / Uncheck
        $col.find(':checkbox:enabled:visible').prop('checked', isChecked);
        $col.addClass('SD_All_Checker_checked-col');
        var spliceIndex = notfounds.indexOf(label);
        if(spliceIndex >= 0) {
          notfounds.splice(spliceIndex, 1);
        } else {
          // Find more than one 
          duplicates.push(label);
        }
      }
    });
    var mesCls = '';
    var actionMes = 'Done!';
    var errMes = '';
    var checkMes = isChecked ? 'Checked' : 'Unchecked';
    var checkedCount = targetFields.length - notfounds.length;
    if(notfounds.length > 0) {
      mesCls = ' error';
      actionMes = 'Error!';
      errMes = ' Not found fields: <strong>' + notfounds.join(', ') + '</strong>.';
    }
    if(duplicates.length > 0) {
      mesCls = ' warning';
      actionMes = 'Warning!';
      errMes = ' Dunplicate fields: <strong>' + duplicates.join(', ') + '</strong>.';
    }
    var $message = jQuery('<tr><td colspan="2"><div class="message'+mesCls+'" id="SD_All_Checker_message">'+actionMes+' '+checkMes+' <strong>'+checkedCount+'</strong> fields.'+errMes+'</div></td><tr>');
    $message.appendTo($table.closest('.bPageBlock').find('.pbHeader > table').first());
    var closeTime = (mesCls == '') ? 5000 : 10000;
    setTimeout(function(){ $message.remove(); }, closeTime);
  }

  $table.find('.SD_ALL_Check_multi-check').each(function(){
    var $link = jQuery(this);
    var targetcls = $link.data('target');
    $link.webuiPopover({
      title: 'Multi Checker', 
      content: function(){ 
        var html = _this._getMultiCheckerPopoverHtml();
        return html;
      }, 
      onShow: function($element) {
        console.log('$element', $element);
        var $textarea = $element.find('textarea[name="fields"]').first();
        $textarea.val('');

        $element.find('.apply-btn').off().click(function(ev){
          var $applyBtn = jQuery(this);
          var isChecked = ($applyBtn.data('target') == 'check') ? true : false;
          var fields = $textarea.val();
          multiCheckApply(targetcls, fields, isChecked);
          $link.webuiPopover('hide');
          ev.stopPropagation();
        });
      },
      onHide: function($element) {
        /*$element.find('.collapse').off();*/
      }, placement:'left-bottom', multi:false, closeable:true, trigger:'click'});
  });
}
/** Multi-Chcker Popover html content */
SD_AllChecker.prototype._getMultiCheckerPopoverHtml = function(){
  var hint = 'Input fields with comma-separated or newline-separated.\ne.g. \n　Field A,Field B\nOR\n　Field A\n　Field B';
  var html = '<div class="multi-checker-body">';
  html  += '<textarea name="fields" rows="10" cols="64" placeholder="'+hint+'"></textarea>';
  html  += '<p class="hint-inner"><b>Tips:</b> Copy and Paste fields from Excel to avoid mistake and save time.</p>';
  html  += '</div><div class="multi-checker-footer">';
  html  += '<button class="btn btnSpacerRight apply-btn" data-target="uncheck">　<input type="checkbox" value="1" /> Uncheck　</button>&nbsp;&nbsp;';
  html  += '<button class="btn apply-btn" data-target="check">　<input type="checkbox" value="1" checked="checked" /> Check　</button>';
  html  += '</div>';
  return html;
}

