/*******************************************************************************
 * SD_FieldMassEdit Class
 ******************************************************************************/ 
var SD_FieldMassEdit = function(options){
  options = options || {};
  this.conn = options.conn;
  this.isLEX = false; // TODO Lightning
  this.sObj = null;

  // 语言包
  var language = (this.conn.userInfo && this.conn.userInfo.language) ? this.conn.userInfo.language : 'en_US';
  if(!T_Languages.hasOwnProperty(language)) language = 'en_US';
  this.langs = T_Languages[language];
}

SD_FieldMassEdit.prototype.activeOnClassic = function(){
  var self = this;
  //Get sObject info
  self.sObj = self.checkSetting(window.location.href);
  //console.log('>>>> self.sObj ', self.sObj);
  if(!self.sObj) return;

  var $target = jQuery('#CustomFieldRelatedList');
  if(self.sObj.custom && self.sObj.objectId) {
    $target = jQuery('#'+self.sObj.objectId.substring(0, 15)+'_CustomFieldRelatedList');
  }
  //console.log('>>>> $target ', $target);
  if($target.length == 0) return;
  // Stylesheet
  var css = '#SD_MassEdit_Edit_Button { color: #015ba7; }';
     css += 'body .bPageBlock .SD_MassEdit_Button_Col{ position: relative; }';
     css += 'body .bPageBlock .SD_MassEdit_Button_Col .badge{ position: absolute; top: -9px; font-size: 75%; font-weight: bold; color: red; right: -14px; }';
     css += 'body .bPageBlock .SD_MassEdit_small{ font-size: 90%; color: grey; font-weight: 400;}';
     css += 'body .bPageBlock .SD_MassEdit_hide{ display: none; }';
     css += 'body .bPageBlock .btn.disabled{ cursor: default; color: rgb(204, 204, 204); border-color: rgb(204, 204, 204); }';
     css += 'body .bPageBlock #SD_MassEdit_THeader .text-right{ text-align: right; font-weight: 400; }';
     css += 'body .bPageBlock #SD_MassEdit_THeader .btn{ padding: 4px 3px; }';
     css += 'body .bPageBlock #SD_MassEdit_ObjectSel{ width: 200px; height: 20px; margin-left : 5px; }';
     css += 'body .bPageBlock .message{ padding : 5px; margin : 0 7px; border:1px solid #390; border-radius: 4px; background-color: #ffc; font-weight: 500; }';
     css += 'body .bPageBlock .message.error{ border-color: #c00; color: #cc0000; }';
     css += 'body .bPageBlock .message.warning{ border-color: #f90; font-weight: 400; color: #000; }';
     css += 'body .bPageBlock .cloned-row > th, body .bPageBlock .cloned-row > td { background-color: #e3f3ff; }';
     css += 'body .bPageBlock .renamed-row > th, body .bPageBlock .renamed-row > td { background-color: #e3f3ff; }';
     css += 'body .bPageBlock .updated-row > th, body .bPageBlock .updated-row > td { background-color: #e3f3ff; }';
     css += 'body .bPageBlock .error-row > th, body .bPageBlock .error-row > td{ background-color: #ffc !important; }';

  jQuery('<style>').prop('type', 'text/css').html(css).appendTo('head');

  var $header = $target.find('.bPageBlock').first();
  var massEditBtn = '&nbsp;<span class="SD_MassEdit_Button_Col"><input type="button" class="btn" id="SD_MassEdit_Edit_Button" value="'+self.langs.BTN_MASS_EDIT+'" href="javascript:void(0)" /></span>';
    massEditBtn += '<span class="SD_MassEdit_small">&nbsp;(<a href="'+T_AppConfig.appUrl+'" target="_blank">'+T_AppConfig.appName+'</a>)</span>';
  $header.find('.pbButton').append(massEditBtn);

  //console.log('>>>. self.sObj ', self.sObj);

  this.initListener({ target : $target });
}


/**
 * options : { target : $target }
 */
SD_FieldMassEdit.prototype.initListener = function(options){
  var self = this;
  var $target = options.target;
  
  $target.find('#SD_MassEdit_Edit_Button').off().click(function(ev) {
    self.activeEditModeClassic($target);
    self.setRowListenerClassic($target);
  });
}

SD_FieldMassEdit.prototype.activeEditModeClassic = function($target){
  var self = this;
  // Replace Header
  var objectSelectHtml = this.getObjectPicklist();
  var $tHead = $target.find('.pbHeader').first();
  var trHeadHtml = '<tr id="SD_MassEdit_THeader"><td class="pbButton">';
     trHeadHtml += '<input type="button" class="btn disabled" disabled="disabled" id="SD_MassEdit_Delete_Button" value="'+self.langs.BTN_DELETE+'" href="javascript:void(0)" /> | ';
     trHeadHtml += '<input type="button" class="btn disabled" disabled="disabled" id="SD_MassEdit_Clone_Button" value="'+self.langs.BTN_CLONE+'" href="javascript:void(0)" /> | ';
     trHeadHtml += '<input type="button" class="btn disabled" disabled="disabled" id="SD_MassEdit_CloneTo_Button" value="'+self.langs.BTN_CLONE_TO+'" href="javascript:void(0)" />'+objectSelectHtml+'</td>';
     trHeadHtml += '<td class="pbButton">&nbsp;</td>';
     trHeadHtml += '<td class="pbButton" style="text-align:right;"><input type="button" class="btn SD_MassEdit_hide" id="SD_MassEdit_Save_Button" value="'+self.langs.BTN_SAVE+'" href="javascript:void(0)" />&nbsp;';
     trHeadHtml += '<input type="button" class="btn SD_MassEdit_hide" id="SD_MassEdit_Quick_Save_Button" value="'+self.langs.BTN_QUICK_SAVE+'" href="javascript:void(0)" />&nbsp;';
     trHeadHtml += '<input type="button" class="btn" id="SD_MassEdit_Cancel_Button" value="'+self.langs.BTN_CANCEL+'" href="javascript:void(0)" /></td></tr>';
     trHeadHtml += '<tr class="SD_MassEdit_hide"><th colspan="3"><div class="message" id="SD_MassEdit_message"></div></th></tr>';
  $tHead.find('tbody tr').addClass('SD_MassEdit_hide');
  $tHead.find('tbody').append(trHeadHtml);
  
  // Replace Body
  var $tBody = $target.find('.pbBody').first();
  $tBody.addClass('SD_MassEditMode');
  var allChkHtml = '<th class="chkColumn"><input type="checkbox" id="SD_MassEdit_allBox" class="head-checkbox" value="1"></th>';
  var rowChkHtml = '<td class="chkColumn"><input type="checkbox" class="row-checkbox" value="1"><input type="hidden" name="hasChanged" value="0" /></td>'
  $tBody.find('tr.headerRow').prepend(allChkHtml);
  $tBody.find('tr.dataRow').prepend(rowChkHtml);
  $tBody.find('.actionColumn, .iconColumn').addClass('SD_MassEdit_hide');
  // Total Row
  var $tRow = $tBody.find('tr.totalRow');
  if($tRow.length > 0) $tRow.prepend('<td class="chkColumn">&nbsp;</td>');
  // Field
  $tBody.find('.dataRow').each(function(){
    var $tr = jQuery(this);
    var $labelCol = $tr.find('.CfLabelCol').first();
    
    var labelText = $labelCol.text();
    var editHtml = '<input type="text" value="'+labelText+'" class="SD_MassEdit_fLabel" data-value="'+labelText+'" />';
    $labelCol.addClass('SD_MassEdit_hide').after(editHtml);

    var $valCol = $tr.find('.dataCell').eq(1);
    var valText = $valCol.text();
    var valHtml = '<input type="text" value="'+valText.replace('__c', '')+'" class="SD_MassEdit_fValue" data-value="'+valText+'" />';
    $valCol.empty().append(valHtml);
  });
}

// Init listener in field table
SD_FieldMassEdit.prototype.setRowListenerClassic = function($target){
  var self = this;
  var $editButtons = jQuery('#SD_MassEdit_Delete_Button,#SD_MassEdit_Clone_Button,#SD_MassEdit_CloneTo_Button,#SD_MassEdit_ObjectSel');
  var $saveButtons = jQuery('#SD_MassEdit_Save_Button,#SD_MassEdit_Quick_Save_Button');
  
  self.setRowInputListener($target);

  // All Checkbox onclick
  jQuery('#SD_MassEdit_allBox').click(function(){
    var checked = jQuery(this).is(':checked');
    $target.find('tr.dataRow .row-checkbox').prop('checked', checked);
    if(checked){
      // Remove Button status
      $editButtons.removeClass('disabled').removeAttr('disabled');
    } else {
      $editButtons.addClass('disabled').attr('disabled', 'disabled');
    }
  });

  // Row Checkbox onclick
  var $checkboxs = $target.find('.row-checkbox');
  $checkboxs.click(function(){
    var allChecked = self.isAllChecked($target);
    jQuery('#SD_MassEdit_allBox').prop('checked', allChecked);

    var hasChecked = (allChecked) ? true : self.hasChecked($target);
    if(hasChecked){
      // Remove Button status
      $editButtons.removeClass('disabled').removeAttr('disabled');
    } else {
      $editButtons.addClass('disabled').attr('disabled', 'disabled');
    }
  });

  // Delete Button onclick
  jQuery('#SD_MassEdit_Delete_Button').click(function(){
    if(!window.confirm(self.langs.DELETE_SURE)) return;
    var $btn = jQuery(this);
    self.startBtnLoading($btn);
    self.clearErrorMessage();
    // 項目削除
    try{
      self.deleteFields($target, function(success){
        self.stopBtnLoading($btn);
        if(success){}
      });
    } catch(ex) {
      return self.handleError(ex);
    }
    T_Util.gaTrack({ category : 'Classic', action : 'massDelete', label : 'Mass Delete Fields' });
  });

  // Clone Button onclick
  jQuery('#SD_MassEdit_Clone_Button').click(function(){
    self.cloneFields($target);
    if($saveButtons.hasClass('SD_MassEdit_hide')) $saveButtons.removeClass('SD_MassEdit_hide');
    T_Util.gaTrack({ category : 'Classic', action : 'massClone', label : 'Mass Clone Fields' });
  });

  // Clone Button onclick
  jQuery('#SD_MassEdit_CloneTo_Button').click(function(){
    var $btn = jQuery(this);
    self.startBtnLoading($btn);
    self.clearErrorMessage();

    self.cloneToFields($target, function(success){
      self.stopBtnLoading($btn);
      if(success){}
    });
    T_Util.gaTrack({ category : 'Classic', action : 'massCloneTo', label : 'Mass Clone Fields To Object' });
  });
  
  // Clone Button onclick
  jQuery('#SD_MassEdit_Cancel_Button').click(function(){
    window.location.reload(true);
  });

  // Save Button onclick
  $saveButtons.click(function(){
    var $btn = jQuery(this);
    self.startBtnLoading($btn);
    self.clearErrorMessage();
    // 項目削除
    try{
      self.saveFields($target, function(success){
        self.stopBtnLoading($btn);
        if(success && $btn.attr('id') == 'SD_MassEdit_Save_Button') {
          // Close Window
          window.location.reload(true);
        }
      });
    } catch(ex) {
      return self.handleError(ex);
    }
    T_Util.gaTrack({ category : 'Classic', action : 'massSave', label : 'Mass Edit Save' });
  });
}
/******************* Private Function ********************/
// Init listener in field table
SD_FieldMassEdit.prototype.setRowInputListener = function($target){
  var $saveButtons = jQuery('#SD_MassEdit_Save_Button,#SD_MassEdit_Quick_Save_Button');
  // Input Changed
  $target.find('input[type="text"].SD_MassEdit_fLabel').change(function(){
    jQuery(this).attr('value', jQuery(this).val()); // HTML value doesn't change issue
    var $row = jQuery(this).closest('tr.dataRow');
    $row.find('input[name="hasChanged"]').val('1');
    if(!$row.hasClass('updated-row')) $row.addClass('updated-row');
    if($saveButtons.hasClass('SD_MassEdit_hide')) $saveButtons.removeClass('SD_MassEdit_hide');
  });
  $target.find('input[type="text"].SD_MassEdit_fValue').change(function(){
    jQuery(this).attr('value', jQuery(this).val()); // HTML value doesn't change issue
    var $row = jQuery(this).closest('tr.dataRow');
    $row.find('input[name="hasChanged"]').val('1');
    if(!$row.hasClass('renamed-row')) $row.addClass('renamed-row');
    if($saveButtons.hasClass('SD_MassEdit_hide')) $saveButtons.removeClass('SD_MassEdit_hide');
  });
}

// 是否全选
SD_FieldMassEdit.prototype.isAllChecked = function($target){
  var allChecked = true;
  $target.find('.row-checkbox').each(function(){
    if(!jQuery(this).is(':checked')) {
      allChecked = false;
      return allChecked;
    }
  });
  return allChecked;
}

// 是否有打勾的项目
SD_FieldMassEdit.prototype.hasChecked = function($target){
  var hasChecked = false;
  $target.find('.row-checkbox').each(function(){
    if(jQuery(this).is(':checked')) {
      hasChecked = true;
      return hasChecked;
    }
  });
  return hasChecked;
}

// 删除所选字段
SD_FieldMassEdit.prototype.deleteFields = function($target, callback){
  var self = this;
  var objectName = self.sObj.name;
  var fieldTargets = {};
  $target.find('.dataRow').each(function(){
    var $row = jQuery(this);
    var $checkbox = $row.find('.row-checkbox').first();
    if(!$checkbox.is(':checked')) return;
    var fName = $row.find('.SD_MassEdit_fValue').first().data('value');
    fieldTargets[objectName + '.' + fName] = { row : $row };
  });
  var fullNames = Object.keys(fieldTargets);
  //console.log('>>> fieldTargets', fieldTargets);
  if(fullNames.length == 0) return;
  // Call delete metadata API
  self.bulkMetadataRequest('delete', 'CustomField', fullNames, function(err, results){
    if (err) { console.error(err); }
    var errorMessages = self.handleApiResponse(fieldTargets, results, function($row) {
       // Remove row when success
       //$row.hide();
       $row.remove();
    });
    if(errorMessages) {
      // Has Error
      self.showPanelMessage({ type: 'ERROR', message : errorMessages.join('<br />') });
      return callback(false);
    } else {
      // Success
      self.showPanelMessage({ message : 'Done!' });
      setTimeout(function(){ self.hidePanelMessage() }, 4000);
      return callback(true);
    }
  }); // conn.deleteMetadata end
}


// Clone所选字段
SD_FieldMassEdit.prototype.cloneFields = function($target, callback){
  var self = this;
  var existLabels = [];
  $target.find('.SD_MassEdit_fLabel').each(function(){
    existLabels.push(jQuery(this).val());
  });
  var existValues = [];
  $target.find('.SD_MassEdit_fValue').each(function(){
    existValues.push(jQuery(this).val());
  });
  // Clone Html 
  var $header = $target.find('.headerRow').first();
  $target.find('.dataRow').each(function(){
    var $row = jQuery(this);
    var $checkbox = $row.find('.row-checkbox').first();
    if(!$checkbox.is(':checked')) return;
    var fName = $row.find('.SD_MassEdit_fValue').first().val();
    var fLabel = $row.find('.SD_MassEdit_fLabel').first().val();
    var fNameSource = $row.find('.SD_MassEdit_fValue').first().data('value');
    var fLabelSource = $row.find('.SD_MassEdit_fLabel').first().data('value');
    var newFLabel = self.getCloneName(existLabels, fLabel);
    var newFName = self.getCloneName(existValues, fName);
    var fProp = $row.find('.dataCell').eq(2).text();
    var trHtml = '<tr class="dataRow cloned-row"><td class="chkColumn"><input type="checkbox" class="checkbox" disabled="disabled" value="1">';
       trHtml += '<input type="hidden" name="hasChanged" value="1"></td>';
       trHtml += '<th class="dataCell"><input type="text" value="'+newFLabel+'" class="SD_MassEdit_fLabel" data-source="'+fLabelSource+'"></th>';
       trHtml += '<td class="dataCell"><input type="text" value="'+newFName+'" class="SD_MassEdit_fValue" data-source="'+fNameSource+'"></td>';
       trHtml += '<td class="dataCell">'+fProp+'</td>';
       trHtml += '<td class="dataCell">&nbsp;</td>';
       trHtml += '<td class="dataCell">&nbsp;</td>';
       trHtml += '<td class="dataCell">&nbsp;</td></tr>';
    var $newRow = jQuery(trHtml);
    $header.after($newRow);
    self.setRowInputListener($newRow);
  });
  
}

// Clone到别的Object
SD_FieldMassEdit.prototype.cloneToFields = function($target, callback){
  var self = this;
  var objectName = self.sObj.name;
  var targetObjectName = jQuery('#SD_MassEdit_ObjectSel').val();
  if(T_Util.isBlank(targetObjectName)) {
    alert(self.langs.ERROR_REQUIRE_OBJECT);
    return callback(false);
  }
  if(targetObjectName == objectName) {
    alert(self.langs.ERROR_DIFFERENT_OBJECT);
    return callback(false);
  }
  var targetSObject = self.conn.metadataNameMap[targetObjectName];
  var fieldTargets = {};
  var newObjectFields = {};
  $target.find('.dataRow').each(function(){
    var $row = jQuery(this);
    var $checkbox = $row.find('.row-checkbox').first();
    if(!$checkbox.is(':checked')) return;
    var fName = $row.find('.SD_MassEdit_fValue').first().val();
    var fullName = objectName + '.' + fName + '__c';
    fieldTargets[fullName] = { name : fName + '__c', row : $row };
    var newFullName = targetObjectName + '.' + fName + '__c';
    newObjectFields[newFullName] = { name : fName + '__c', row : $row };
  });
  var sourceFullNames = Object.keys(fieldTargets);
  if(sourceFullNames.length == 0) return resolve(true);

  //console.log('>>>> self.conn.readMetadata ', sourceFullNames);
  self.bulkMetadataRequest('read', 'CustomField', sourceFullNames, function(err, results){
    //console.log('>>>> self.conn.readMetadata results ', results);
    if (err) { console.error(err); return callback(false); }
    if(!Array.isArray(results)) results = [results];
    var newMetadata = [];
    for(i = 0; i < sourceFullNames.length; i++) {
      var cloneTarget = fieldTargets[sourceFullNames[i]];
      for(ri = 0; ri < results.length; ri++) {
        if(results[ri].fullName == sourceFullNames[i]) {
          var newMeta = results[ri];
          // Replace Label and name
          newMeta['fullName'] = targetObjectName + '.' + cloneTarget.name;  // Target Object field name
          if(newMeta.type == 'Lookup' || newMeta.type == 'MasterDetail' && newMeta.relationshipName) {
            newMeta['relationshipName'] = newMeta.relationshipName + '_' + T_Util.getRandom(3);
          }
          newMetadata.push(newMeta);
        }
      }
    } // .for
    // Create Metadata
    //console.log('>>>> self.conn.createMetadata ', newMetadata);
    self.bulkMetadataRequest('create', 'CustomField', newMetadata, function(err, results){
      //console.log('>>>> self.conn.createMetadata ', err, results);
      if (err) { console.error(err); return callback(false); }
      var errorMessages = self.handleApiResponse(newObjectFields, results, function($row) {});
      if(errorMessages) {
        // Has Error
        self.showPanelMessage({ type: 'ERROR', message : errorMessages.join('<br />') });
        return callback(false);
      } else {
        // Success
        var settingUrl = '/p/setup/layout/LayoutFieldList?type=' + targetSObject.name + '&setupid=' + targetSObject.name + 'Fields';
        if(targetSObject.custom && typeof targetSObject.objectId != 'undefined'){
          settingUrl = '/' + targetSObject.objectId;
        }
        var linkHtml = self.langs.GOTO_OBJECT_SETTING.replace('{OBJECT}', '<a href="'+settingUrl+'" target="_blank">'+targetSObject.label+'</a>');
        self.showPanelMessage({ message : 'Done! ' + linkHtml});
        setTimeout(function(){ self.hidePanelMessage() }, 10000);
        return callback(true);
      }
    }); // .self.conn.createMetadata

  });
}

// Saveアクション
SD_FieldMassEdit.prototype.saveFields = function($target, callback){
  var self = this;
  var objectName = self.sObj.name;
  // Validation
  var existValues = [];
  var hasError = false;
  $target.find('.SD_MassEdit_fValue').each(function(){
    var fName = jQuery(this).val();
    if(existValues.indexOf(fName) >= 0) {
      var $row = jQuery(this).closest('.dataRow');
      $row.addClass('error-row');
      self.showPanelMessage({ type: 'ERROR', message : 'Duplicate field name. Field : ' + fName });
      hasError = true;
      return false;
    }
    existValues.push(fName);
  });
  if(hasError) return callback(false);

  // Cloned, Renamed, Updated
  self.saveClonedFields($target)
  .then(function(result){
    return self.saveUpdateFields($target);
  })
  .then(function(result){
    return self.saveRenameFields($target);
  })
  .then(function(result){
    // Success
    self.showPanelMessage({ message : 'Done!' });
    setTimeout(function(){ self.hidePanelMessage() }, 4000);
    return callback(true);
  })
  .catch(function(err) {
    if(Array.isArray(err)) {
      self.showPanelMessage({ type: 'ERROR', message : err.join('<br />') });
    } else {
      self.handleError(err);
    }
    return callback(false);
  });

}
// Save Cloned Fields
// return {Promise.<Object>}
SD_FieldMassEdit.prototype.saveClonedFields = function($target){
  var self = this;
  var objectName = self.sObj.name;
  return new Promise(function(resolve, reject) {
    // Clone fields
    var fieldTargets = {};
    var cloneSources = [];
    $target.find('.dataRow.cloned-row').each(function(){
      var $row = jQuery(this);
      var fName = $row.find('.SD_MassEdit_fValue').first().val();
      var fLabel = $row.find('.SD_MassEdit_fLabel').first().val();
      var fullName = objectName + '.' + fName + '__c';
      var fNameSource = $row.find('.SD_MassEdit_fValue').first().data('source');
      var fullNameSource = objectName + '.' + fNameSource;
      cloneSources.push(fullNameSource);
      fieldTargets[fullName] = { label : fLabel, source : fullNameSource, row : $row };
    });
    if(cloneSources.length == 0) return resolve(true);

    //console.log('>>>> self.conn.readMetadata ', cloneSources);
    self.bulkMetadataRequest('read', 'CustomField', cloneSources, function(err, results){
      //console.log('>>>> self.conn.readMetadata results ', results);
      if (err) { return reject(err); }
      if(!Array.isArray(results)) results = [results];
      var newMetadata = [];
      var cloneFullNames = Object.keys(fieldTargets);
      for(i = 0; i < cloneFullNames.length; i++) {
        var cloneTarget = fieldTargets[cloneFullNames[i]];
        for(ri = 0; ri < results.length; ri++) {
          if(results[ri].fullName == cloneTarget.source) {
            var newMeta = results[ri];
            // Replace Label and name
            newMeta['fullName'] = cloneFullNames[i];  // Cloned field name
            newMeta['label'] = cloneTarget.label;
            if(newMeta.type == 'Lookup' || newMeta.type == 'MasterDetail' && newMeta.relationshipName) {
              newMeta['relationshipName'] = newMeta.relationshipName + '_' + T_Util.getRandom(3);
            }
            newMetadata.push(newMeta);
          }
        }
      } // .for
      // Create Metadata
      //console.log('>>>> self.conn.createMetadata ', newMetadata);
      self.bulkMetadataRequest('create', 'CustomField', newMetadata, function(err, results){
        //console.log('>>>> self.conn.createMetadata results ', err, results);
        if (err) { return reject(err); }
        var errorMessages = self.handleApiResponse(fieldTargets, results, function($row) {
          $row.find('input[name="hasChanged"]').val('0');
          $row.removeClass('cloned-row');
          //$row.removeClass('cloned-row').removeClass('highlight');
        });
        if(errorMessages) return reject(errorMessages);
        return resolve(true);
      }); // .self.conn.createMetadata

    });
  }); // .Promise
}

// Save Renamed Fields
// return {Promise.<Object>}
SD_FieldMassEdit.prototype.saveRenameFields = function($target){
  var self = this;
  var objectName = self.sObj.name;
  return new Promise(function(resolve, reject) {
    // Rename fields
    var fieldTargets = {};
    $target.find('.dataRow.renamed-row').each(function(){
      var $row = jQuery(this);
      var $changed = $row.find('input[name="hasChanged"]').first();
      if($changed.val() == '0') return;
      var $valIpt = $row.find('.SD_MassEdit_fValue').first();
      var fullName = $valIpt.val() + '__c';
      if(fullName == $valIpt.data('value')) return;
      var newFullName = objectName + '.' + fullName;
      var oldFullName = objectName + '.' + $valIpt.data('value');
      fieldTargets[oldFullName] = { to : newFullName, row : $row };
    });
    if(Object.keys(fieldTargets).length == 0) return resolve(true);
    // Loop to change name of field
    async.eachLimit(Object.keys(fieldTargets), 5, function(oldFullName, next) {
      var newFullName = fieldTargets[oldFullName].to;
      //console.log('>>>> self.conn.renameMetadata ', oldFullName, newFullName);
      self.conn.renameMetadata('CustomField', oldFullName, newFullName, function(err, results){
        //console.log('>>>> self.conn.renameMetadata results ', err, fieldTargets, results);
        if (err) { return next(err); }
        var errorMessages = self.handleApiResponse(fieldTargets, results, function($row) {
          $row.find('input[name="hasChanged"]').val('0');
          $row.removeClass('renamed-row');
          var $valIpt = $row.find('.SD_MassEdit_fValue').first();
          $valIpt.data('value', $valIpt.val() + '__c');
          //$row.removeClass('cloned-row').removeClass('highlight');
        });
        if(errorMessages) return resolve(errorMessages);
        next();
      }); // .self.conn.renameMetadata
    }, function(err) {
      if (err) { return reject(err); }
      return resolve(true);
    }); // .async.eachLimit
  }); // .Promise
}

// Update Field Label
// return {Promise.<Object>}
SD_FieldMassEdit.prototype.saveUpdateFields = function($target){
  var self = this;
  var objectName = self.sObj.name;
  return new Promise(function(resolve, reject) {
    // Rename fields
    var fieldTargets = {};
    $target.find('.dataRow.updated-row').each(function(){
      var $row = jQuery(this);
      var $changed = $row.find('input[name="hasChanged"]').first();
      if($changed.val() == '0') return;
      var $labelIpt = $row.find('.SD_MassEdit_fLabel').first();
      var label = $labelIpt.val();
      if(label == $labelIpt.data('value')) return;
      var $valIpt = $row.find('.SD_MassEdit_fValue').first();
      var fullName = objectName + '.' + $valIpt.data('value');
      fieldTargets[fullName] = { label : label, row : $row };
    });
    var sourceFullNames = Object.keys(fieldTargets);
    //console.log('>>>> fieldTargets', fieldTargets);
    if(sourceFullNames.length == 0) return resolve(true);
    self.bulkMetadataRequest('read', 'CustomField', sourceFullNames, function(err, results){
      //console.log('>>>> self.conn.readMetadata results ', results);
      if (err) { return reject(err); }
      if(!Array.isArray(results)) results = [results];
      var newMetadata = [];
      
      for(i = 0; i < results.length; i++) {
        var updateTarget = fieldTargets[results[i].fullName];
        var newMeta = results[i];
        newMeta['label'] = updateTarget.label;
        if(results[i].length) {
          newMeta['length'] = results[i].length;
        }
        newMetadata.push(newMeta);
      } // .for
      //console.log('>>>> self.conn.updateMetadata ', newMetadata);
      self.bulkMetadataRequest('update', 'CustomField', newMetadata, function(err, results){
        //console.log('>>>> self.conn.updateMetadata results ', results);
        if (err) { return reject(err); }
        var errorMessages = self.handleApiResponse(fieldTargets, results, function($row) {
          // Clear hasChanged value if API Name is not be changed
          if(!$row.hasClass('renamed-row')) {
            $row.find('input[name="hasChanged"]').val('0');
          }
          $row.removeClass('updated-row');
        });
        if(errorMessages) return reject(errorMessages);
        return resolve(true);
      }); // .self.conn.updateMetadata

    }); // .self.conn.readMetadata

  });
}

// Bulk read metadata with each 10 records
SD_FieldMassEdit.prototype.bulkMetadataRequest = function(action, metaType, fullNames, callback){
  var self = this;
  // sf:EXCEEDED_ID_LIMIT: EXCEEDED_ID_LIMIT
  if(fullNames.length <= 10) {
    return self.handleMetadataRequest(action, metaType, fullNames, callback);
  }
  var queues = [];
  for(var i = 0; i < fullNames.length; i++) {
    var f = Math.floor(i / 10);
    if(!queues[f]) queues.push([]);
    queues[f].push(fullNames[i]);
  }
  var results = [];
  async.each(queues, function(records, next) {
    //console.log('>>>> bulkMetadataRequest ', queues, records);
    self.handleMetadataRequest(action, metaType, records, function(err, res){
      if(err) return next(err);
      res = (Array.isArray(res)) ? res : [res];
      for(var i = 0; i < res.length; i++) {
        results.push(res[i]);
      }
      next();
    });
  }, function complete(err) {
    return callback(err, results);
  });
}
// Handle metadata api request
SD_FieldMassEdit.prototype.handleMetadataRequest = function(action, metaType, fullNames, callback){
  var self = this;
  if(action == 'read') return self.conn.readMetadata(metaType, fullNames, callback);
  if(action == 'update') return self.conn.updateMetadata(metaType, fullNames, callback);
  if(action == 'create') return self.conn.createMetadata(metaType, fullNames, callback);
  if(action == 'delete') return self.conn.deleteMetadata(metaType, fullNames, callback);
}

// 根据API返回信息进行错误处理
SD_FieldMassEdit.prototype.handleApiResponse = function(fieldTargets, results, successHandler) {
  if(!Array.isArray(results)) results = [results];
  var errorMessages = [];
  for (var i=0; i < results.length; i++) {
    var result = results[i];
    var $row = (!fieldTargets[result.fullName]) ? null : fieldTargets[result.fullName].row;
    if(T_Util.isBlank($row)) continue;
    var fieldLabel = $row.find('.SD_MassEdit_fLabel').first().data('value');
    if(!fieldLabel) { // Cloned field
      fieldLabel = $row.find('.SD_MassEdit_fLabel').first().val();
    }
    if(!result.success && result.errors) {
      var errMes = fieldLabel + ' : ';
      var errors = (Array.isArray(result.errors)) ? result.errors : [result.errors];
      for(var ei = 0; ei < errors.length; ei++) {
        errMes += errors[ei].message;
      }
      errorMessages.push(errMes);
      $row.addClass('error-row');
    } else {
      // Success handler
      if(successHandler) successHandler($row);
    }
  }
  var result = (errorMessages.length > 0) ? errorMessages : null;
  return result;
}

// カスタム設定
SD_FieldMassEdit.prototype.checkSetting = function(url) {
  var self = this;
  var oId = T_Util.getObjectIdFromUri(url);
  if(!T_Util.isBlank(oId)) {
    //先验证是否Custom Object
    //Custom Object
    for(var k in self.conn.metadata){
      var sobj = self.conn.metadata[k];
      if(sobj.objectId == oId){
        return sobj;
      }
    }
  }

  //验证是否标准Object
  var parser = T_Util.getParser(url);
  if(self.isLEX) {
    // Lightning
    var paths = parser.pathname.split('/');
    if(paths.length < 6) return null;
    if(paths[1] == 'lightning' && paths[2] == 'setup' && paths[3] == 'ObjectManager') {
      var sobjectName = paths[4];
      if(self.conn.metadataNameMap.hasOwnProperty(sobjectName)) {
        return self.conn.metadataNameMap[sobjectName];
      }
    }
    return false;
  }
  
  var sobjectName = T_Util.getParam('type', url);
  var setupid = T_Util.getParam('setupid', url);
  //log('>>>> sobjectName ' + sobjectName);
  if(T_Util.isBlank(sobjectName) || T_Util.isBlank(setupid)) return false;
  if(self.conn.metadataNameMap.hasOwnProperty(sobjectName)) {
    return self.conn.metadataNameMap[sobjectName];
  }
  return false;
}

// Clone To Object一览
SD_FieldMassEdit.prototype.getObjectPicklist = function(options){
  var self = this;
  //console.log('>>>> self.conn.metadataNameMap ', self.conn.metadataNameMap);
  var customGroup = [];
  var standardGroup = [];
  var metaKeys = Object.keys(self.conn.metadataNameMap);
  for(var i = 0; i < metaKeys.length; i++) {
    var meta = self.conn.metadataNameMap[metaKeys[i]];
    if(!meta.createable || !meta.updateable || !meta.layoutable || !meta.queryable || !meta.triggerable) continue;
    var label = meta.label+' ('+meta.name+')';
    if(meta.custom) {
      var option = '<option label="'+label+'" value="'+meta.name+'">'+label+'</option>';
      customGroup.push(option);
    } else {
      var option = '<option label="'+label+'" value="'+meta.name+'">'+label+'</option>';
      standardGroup.push(option);
    }
  }
  var selHtml = '<select name="object" id="SD_MassEdit_ObjectSel" class="disabled" disabled="disabled">';
  selHtml += '<option label="--None--" value="">--None--</option>';
  if(customGroup.length > 0){
    selHtml += '<optgroup label="Custom Objects">'+customGroup.join('')+'</optgroup>';
  }
  if(standardGroup.length > 0){
    selHtml += '<optgroup label="Standard Objects">'+standardGroup.join('')+'</optgroup>';
  }
  selHtml += '</select>';
  return selHtml;
}

// Panel信息显示
SD_FieldMassEdit.prototype.showPanelMessage = function(options){
  options = options || {};
  var $message = jQuery('#SD_MassEdit_message');
  $message.removeClass('error').removeClass('warning').show();
  if(options.type == 'VALIDATE_ERROR') {
    $message.addClass('error').html(options.message);
  } else if(options.type == 'ERROR') {
    $message.addClass('error').html('<strong>Error: </strong>' + options.message);
  } else if(options.type == 'WARNING') {
    $message.addClass('warning').html('<strong>Warning: </strong>' + options.message);
  } else {
    $message.html(options.message);
  }
  $message.closest('tr').removeClass('SD_MassEdit_hide')
}
// Panel信息隐藏
SD_FieldMassEdit.prototype.hidePanelMessage = function(){
  var $message = jQuery('#SD_MassEdit_message');
  $message.closest('tr').addClass('SD_MassEdit_hide');
}

// 清除页面上的Error
SD_FieldMassEdit.prototype.clearErrorMessage = function(options){
  jQuery('#SD_MassEdit_message').closest('tr').addClass('SD_MassEdit_hide');
  var $table = jQuery('.SD_MassEditMode');
  $table.find('.dataRow .error').removeClass('error');
  $table.find('.dataRow .errorMsg').remove();
  $table.find('.dataRow.error-row').removeClass('error-row');
}


