// Get new name for clone field
SD_FieldMassEdit.prototype.getCloneName = function(exists, name){
  for(i = 2; i < 100; i++) {
    var newName = name + i;
    if(exists.indexOf(newName) < 0) return newName;
  }
  return name;
}