/*******************************************************************************
 * SD_App Class
 ******************************************************************************/
var SD_App = function(options){
  this.conn = new SD_OAuthConnect();
  this.userInfo = this.conn.userInfo;

  this.view = new SD_AppView();
  this.authPending = null;
  this.tabid = null;

  this.sobject = null;
  this.isLEX = false;
  this.port = null;

  this.crobQueue = {};
}
/** set oauth connection */
SD_App.prototype.setConn = function(connection){
  this.conn = connection;
}


// Run main script 
SD_App.prototype.run = function(options) {
  var self = this;
  options = options || {};
  self.view.showPanel();
}

// Handle request from popup 
SD_App.prototype.handle = function(options) {
  var self = this;
  options = options || {};
  if(T_Util.isBlank(self.tabid) && options.tabid) {
    self.tabid = options.tabid;
  }
  if(typeof self[options.mode] == 'function') {
    options['callback'] = self.sendResponse;
    self[options.mode](options);
  }
}

// Send request to page 
SD_App.prototype.sendResponse = function(options) {
  options = options || {};
  options['extensionId'] = chrome.runtime.id;
  options['tabid'] = this.tabid;
  chrome.runtime.sendMessage(options);
}

// Fix Chrome CROB Issue on Lightning
// Send api request via popup to avoid Chrome CROB
// options: { api: 'soap', method : 'query', params : '' }
SD_App.prototype.sendCrobRequest = function(options, callback) {
  options = options || {};
  options['extensionId'] = chrome.runtime.id;
  options['tabid'] = this.tabid;
  options['mode'] = 'CROB';
  options['accessToken'] = this.conn.conn.accessToken;
  options['instanceUrl'] = this.conn.conn.instanceUrl;

  var uuid = T_Util.getUUid();
  options['uuid'] = uuid;
  this.crobQueue[uuid] = callback;
  chrome.runtime.sendMessage(options);
}
// Handle CrobRequest callback
SD_App.prototype.callbackCrobRequest = function(options) {
  var self = this;
  options = options || {};
  if(T_Util.isBlank(options.uuid)) return;
  if(!self.crobQueue.hasOwnProperty(options.uuid)) return;
  self.crobQueue[options.uuid](options.err, options.res);
  delete self.crobQueue[options.uuid];
}
/*-----------------------------------------------------------------*
 * Public Functions
 *-----------------------------------------------------------------*/

/**
 * Init with userinfo and metadata
 */
SD_App.prototype.init = function(options) {
  var self = this;
  options = options || {};
  T_Logger.setUserInfo(self.userInfo);

  // 语言包
  var language = self.userInfo.language || 'en_US';
  if(!T_FieldMapping.hasOwnProperty(language)) language = 'en_US';
  T_FieldLabelMap = T_FieldMapping[language];
  var url = window.location.href;
  // Detect is Lightning
  // Fix unable send crob request from Lighting
  var parser = T_Util.getParser(url);
  self.isLEX = T_Util.isLightningDomain(parser);

  self.conn.checkStoredOAuthToken(function(success) {
    T_Logger.log('CHECK TOKEN', success);
    if(!success) return options.callback(self.parsePage());
    // Lightning OR Classic with no session id
    if(T_Util.isNotBlank(self.conn.authInfo)) {
      T_Logger.setUserInfo(self.conn.authInfo);
      // 重设语言包
      self.userInfo = self.conn.authInfo;
      if(language !== self.userInfo.language && self.userInfo.language) {
        language = self.userInfo.language || 'en_US';
        if(!T_FieldMapping.hasOwnProperty(language)) language = 'en_US';
        T_FieldLabelMap = T_FieldMapping[language];
      }
    }
    self.conn.checkStoreMetaData(function(metadata){
      T_Logger.log('CHECK METADATA', metadata);
      if(metadata && !self.conn.metadata) {
        // FIX conn中的metadata莫名null的问题
        self.conn.metadata = metadata;
        self.conn.metadataNameMap = self.conn.converToNameMap(metadata);
        self.conn.metadataPrefixMap = self.conn.converToPrefixMap(metadata);
      }
      var result = self.parsePage();
      if(!metadata) {
        result['page'] = 'unauth';
      }
      return options.callback(result);
    });
  });
}
SD_App.prototype.oauth = function(options) {
  var self = this;
  // Start OAuth2
  var url = self.conn.getAuthorizationUrl({tabid : self.tabid});
  self.authPending = options.callback;
  return window.open(url, 'lightning-oauth-win', 'width=500,height=600,resizable=no,scrollbars=no,menubar=no');
}

// Reload Metadata
SD_App.prototype.reloadMetadata = function(options) {
  var self = this;
  self.conn.restoreMetaData(function(success){
    T_Logger.log('RELOAD METADATA', success);
    if(!success) return options.callback({mode : 'reloadMetadata', done : false});
    return options.callback({ mode : 'reloadMetadata', done : true, metadata : self.conn.metadata });
  });
}

// Resize iframe container
SD_App.prototype.resizePanel = function(options) {
  this.view.resizePanel(options);
}

// Show field api name
SD_App.prototype.showApiName = function(options) {
  var self = this;
  self.conn.describe(self.sobject.name, function(err, res) {
    if(err) return self.handleError(err);
    var fields = res.fields;
    var specialFields = [];
    if(self.sobject.custom == false && T_FieldLabelMap.hasOwnProperty(self.sobject.name)) {
      specialFields = T_FieldLabelMap[self.sobject.name];
    }
    self.view.showFieldApiName(fields, specialFields, self.isLEX);
    return options.callback({ mode : 'showApiName', done : true });
  });
}

// Hide field api name
SD_App.prototype.hideApiName = function(options) {
  var self = this;
  self.view.hideFieldApiName({ isLEX : self.isLEX });
  return options.callback({ mode : 'hideApiName', done : true });
}

// Get field values from detail page
SD_App.prototype.getFieldValues = function(options) {
  var self = this;
  var sObjectName = options.sobject || self.sobject.name;
  self.conn.describe(sObjectName, function(err, res) {
    if(err) return self.handleError(err);
    var fields = res.fields;
    var childRelationships = res.childRelationships;
    //console.log('>>>> describe', sObjectName, res);
    var specialFields = [];
    if(!sObjectName.endsWith('__c') && T_FieldLabelMap.hasOwnProperty(sObjectName)) {
      specialFields = T_FieldLabelMap[sObjectName];
    }
    var sobjMeta = self.conn.metadataNameMap[sObjectName];
    var values = self.view.getObjectValues(fields, specialFields, self.isLEX);
    return options.callback({ mode : 'getFieldValues', fields : fields, values : values, sobject : sobjMeta, childRelationships :  childRelationships});
  });
}

// Get field values for target objects
SD_App.prototype.getMultiObjectFieldValues = function(options) {
  var self = this;
  var sobjects = options.sobjects || [];
  var metadata = {};
  async.eachLimit(sobjects, 5, function(sObjectName, next) {
    //console.log('>>>> queue', queue);
    self.conn.describe(sObjectName, function(err, res) {
      if(err) return self.handleError(err);
      var fields = res.fields;
      //console.log('>>>> describe', sObjectName, res);
      var sobjMeta = self.conn.metadataNameMap[sObjectName];
      metadata[sObjectName] = { fields : fields, sobject : sobjMeta };
      if(options.process) options.process(Object.keys(metadata));
      next();
    });
  }, function() {
    return options.callback({ mode : 'getMultiObjectFieldValues', metadata : metadata});
  });
}

// Do query
SD_App.prototype.query = function(options) {
  var self = this;
  options = options || {};
  var soql = options.soql || '';
  self.conn.query(soql, function(err, res) {
    if(err) {
      // return self.handleError(err);
      res = { error : err };
    }
    res['mode'] = 'query'; 
    return options.callback(res);
  });
}
// Do requestGet
SD_App.prototype.requestGet = function(options) {
  var self = this;
  options = options || {};
  var url = options.url || '';
  self.conn.requestGet(url, function(err, res) {
    if(err) {
      res = { error : err };
    }
    res['mode'] = 'requestGet'; 
    return options.callback(res);
  });
}

// Open Profile Select Modal
SD_App.prototype.describeObjectFull = function(options) {
  var self = this;
  options = options || {};
  var exportWating = false;
  var cacheFullDone = false;
  var result = null;
  var targetProfileIds = [];
  var _exportEvent = function(profileIds) {
    targetProfileIds = profileIds;
    if(cacheFullDone && result && result.profiles) {
      var profileKeys = Object.keys(result.profiles);
      for(var i = 0; i < profileKeys.length; i++) {
        if(targetProfileIds.indexOf(profileKeys[i]) < 0) {
          delete result.profiles[profileKeys[i]];
        }
      }
      // Cache background is done
      self.view.closeProfileViewer();
      return options.callback(result);
    } else {
      // Wait for cache backgroud done
      exportWating = true;
    }
  }
  var _cacheFullBackground = function(profiles) {
    self._describeObjectFull({ sObjects : options.sObjects,
      callback : function(res) {
        cacheFullDone = true;
        result = { mode : 'describeObjectFull', 
                       describes : res.describes,
                       objects : res.objects,
                       triggers : res.triggers,
                       workflows : res.workflows,
                       profiles : profiles,
                  };
        if(exportWating) {
          var profileKeys = Object.keys(result.profiles);
          for(var i = 0; i < profileKeys.length; i++) {
            if(targetProfileIds.indexOf(profileKeys[i]) < 0) {
              delete result.profiles[profileKeys[i]];
            }
          }
          // Export button is clicked
          self.view.closeProfileViewer();
          return options.callback(result);
        }
      } 
    });
  }

  self.conn.getStorageData('profiles', function(profiles) {
    //console.log('>>>> self.conn.getStorageData', profiles);
    //console.log('>>>> self.userInfo', self.userInfo);
    if(profiles && Object.keys(profiles).length > 0) {
      // Load from cache
      self.view.openProfileViewer({ profiles : profiles, cache : true, exportEvent : _exportEvent });
      _cacheFullBackground(profiles);
    } else {
      self._requestProfiles({ cache : true, exportEvent : _exportEvent }, function(profileCacheMap) {
        // DONE
        _cacheFullBackground(profileCacheMap);
      });
    }
  });
}

// load profiles metadata from API and store cache
SD_App.prototype._requestProfiles = function(options, callback) {
  var self = this;
  // Load from metadata API
  self.conn.listProfiles(function(err, profileList) {
    if(err) return callback(err);
    var fullNames = [];
    var profileIdMap = {};
    var profileCacheMap = {}; // { profileId : { fullName : '', ... } }
    for(var i = 0; i < profileList.length; i++) {
      //.replace(/%20/g, '+')
      var profile = profileList[i];
      var key = encodeURIComponent(profile.fullName);
      fullNames.push(profile.fullName);
      profileIdMap[key] = profile.id;
      profileCacheMap[profile.id] = profile;
    }
    //console.log('>>>> self.conn.listProfiles', profileList);
    self.view.openProfileViewer({ profiles : profileCacheMap, exportEvent : options.exportEvent });
    fullNames = T_Util.splitQueue(fullNames);
    async.eachLimit(fullNames, 5, function(queue, next) {
      //console.log('>>>> queue', queue);
      self.conn.getProfiles(queue, function(err, profiles) {
        if(err) return callback(err);
        //console.log('>>>> profiles', profiles);
        if(!Array.isArray(profiles)) profiles = [profiles];
        for(var i = 0; i < profiles.length; i++) {
          var profile = profiles[i];
          var key = encodeURIComponent(profile.fullName);
          var profileId = profileIdMap[key];
          if(!profileId) continue;
          profiles[i]['id'] = profileId;
          profileCacheMap[profileId] = Object.assign(profileCacheMap[profileId], profile);
        }
        self.view.updateProfileViewer({ profiles : profiles });
        //result.profiles = result.profiles.concat(profiles);
        next();
      });
    }, function() {
      //console.log('>>>> profiles done, set profileCacheMap', profileCacheMap);
      self.conn.saveStorageData('profiles', profileCacheMap, function(success) {});
      // Done
      self.view.activeProfileViewer();
      return callback(profileCacheMap);
    });

  });

}

// describe Object, Fields, Workflows, Approval Processes, Triggers, Flows metadata
SD_App.prototype._describeObjectFull = function(options) {
  var self = this;
  options = options || {};
  var sObjects = options.sObjects || '';
  var result = { describes : [], objects : [], triggers : [], workflows : []};
  // Splite array with 10 items
  var _splitQueue = function(targets) {
    var exceededLimit = 10;
    if(targets.length > exceededLimit) {
      var pages = [];
      for(var i = 0; i < targets.length; i++) {
        if(i % exceededLimit == 0) {
          pages.push([]);
        }
        pages[(pages.length - 1)].push(targets[i]);
      }
      targets = pages;
    } else {
      targets = [targets];
    }
    return targets;
  }

  async.parallel([
    // describe SObjects
    function(callback) {
      self.conn.describeSObjects(sObjects, function(err, describes) {
        if(err) return callback(err);
        //console.log('>>>> request result.describes', describes);
        result.describes = describes;
        return callback();
      });
    },
    // get ValidationRule
    function(callback) {
      var fullNames = [].concat(sObjects);
      fullNames = _splitQueue(fullNames);
      async.eachLimit(fullNames, 5, function(queue, next) {
        self.conn.getObjects(queue, function(err, objects) {
          if(err) return callback(err);
          if(!Array.isArray(objects)) objects = [objects];
          for(var i = 0; i < objects.length; i++) {
            result.objects.push(objects[i]);
          }
          //console.log('>>>> request result.objects', result.objects);
          next();
        });
      },callback);
    },
    // get Trigger
    function(callback) {
      var triggerSoql = 'SELECT Id, Name, TableEnumOrId, ApiVersion, Body, IsValid, ';
         triggerSoql += 'UsageBeforeInsert, UsageAfterInsert, UsageBeforeUpdate, UsageAfterUpdate, UsageBeforeDelete, UsageAfterDelete, UsageIsBulk, UsageAfterUndelete ';
         triggerSoql += 'FROM ApexTrigger WHERE TableEnumOrId IN (\'' + sObjects.join('\', \'') + '\')';
      self.conn.query(triggerSoql, function(err, triggers) {
        if(err) return callback(err);
        result.triggers = triggers.records;
        //console.log('>>>> request result.triggers', result.triggers);
        return callback();
      });
    },
    // get Workflow
    function(callback) {
      var fullNames = [].concat(sObjects);
      fullNames = _splitQueue(fullNames);
      async.eachLimit(fullNames, 5, function(queue, next) {
        self.conn.getWorkflows(queue, function(err, workflows) {
          if(err) return callback(err);
          if(!Array.isArray(workflows)) workflows = [workflows];
          for(var i = 0; i < workflows.length; i++) {
            result.workflows.push(workflows[i]);
          }
          //console.log('>>>> request result.workflows', result.workflows);
          next();
        });
      },callback);
    },
  ], function(err) {
    if(err) return self.handleError(err);
    //console.log('>>>> request result done', result);
    return options.callback(result);
  });
}

// describeSObjects field metadata
SD_App.prototype.describeSObjects = function(options) {
  var self = this;
  options = options || {};
  var sObjects = options.sObjects || '';
  self.conn.describeSObjects(sObjects, function(err, res) {
    if(err) return self.handleError(err);
    return options.callback({ mode : 'describeSObjects', describes : res });
  });
}

// Get layouts from recordTypeMappings list
SD_App.prototype.getMultiLayouts = function(sObjectName, recordTypes, callback) {
  var self = this;
  var sobjLayouts = [];
  var layoutIds = [];
  async.eachLimit(recordTypes, 5, function(recordType, next) {
    // Filter dumplicate layout
    if(layoutIds.indexOf(recordType.layoutId) >= 0) return next();
    layoutIds.push(recordType.layoutId);

    self.conn.getLayouts(sObjectName, recordType.recordTypeId, function(err, res) {
      //console.log('>>>> getMultiLayouts', sObjectName, recordType.recordTypeId, res);
      var layoutMeta = res;
      layoutMeta['sobject'] = sObjectName;
      layoutMeta['recordType'] = recordType;
      sobjLayouts.push(layoutMeta);
      next();
    });
  }, function(err) {
    if (err) { console.error(err); }
    return callback(err, sobjLayouts);
  });
}

// describeSObjects field metadata
SD_App.prototype.requestPagelayout = function(options) {
  var self = this;
  options = options || {};
  var sObjects = options.sObjects || [];
  var layouts = [];
  var layoutMeta = [];

  async.parallel([
    // get Layout metadata
    function(callback) {
      async.eachLimit(sObjects, 5, function(sObjectName, next) {
        self.conn.getLayouts(sObjectName, function(err, res) {
          //console.log('>>>> requestPagelayout', res);
          if(err) return next(err);
          if(Array.isArray(res.layouts) && res.layouts.length == 0) {
            // Filter custom setting
            return next(err);
          }
          // Single layout
          if(res.layouts && res.recordTypeMappings.length == 1) {
            var layoutMeta = res.layouts[0];
            layoutMeta['sobject'] = sObjectName;
            layoutMeta['recordType'] = res.recordTypeMappings[0];
            layouts.push(layoutMeta);
            next();
          } else {
            // Multiple layouts
            self.getMultiLayouts(sObjectName, res.recordTypeMappings, function(err, metadatas){
              if(err) return next(err);
              layouts = layouts.concat(metadatas);
              next();
            });
          }
        });
      },callback);
    },
    // get Layout ids
    function(callback) {
      self.conn.listLayouts(sObjects, function(err, metadata) {
        if(err) return callback(err);
        layoutMeta = metadata;
        return callback();
      });
    }
  ], function(err) {
    if(err) return self.handleError(err);
    var relatedObjects = self.getRelatedObjFromLayout(layouts);
    for(var i = 0; i < relatedObjects.length; i ++){
      var sobjName = relatedObjects[i];
      if(sObjects.indexOf(sobjName) < 0) sObjects.push(sobjName);
    }
    self.conn.describeSObjects(sObjects, function(err, describes) {
      if(err) return self.handleError(err);
      return options.callback({ mode : 'requestPagelayout', layoutMeta : layoutMeta, layouts : layouts, describes : describes });
    });
  });
}

// describe listview metadata
SD_App.prototype.requestListView = function(options) {
  var self = this;
  options = options || {};
  var sObjects = options.sObjects || [];
  var describes = [];
  var refToDescMap = {}; // Reference字段Key Map
  var refSObjects = []; // 字段中有Reference Object时临时保存用
  var sObjectListviews = {};
  var ListviewReqQueues = [];
  var listviewIndexIdMap = {};
  async.waterfall([
    // get sObjects describes
    function(callback) {
      self.conn.describeSObjects(sObjects, function(err, desc) {
        if(err) return self.handleError(err);
        describes = desc;
        for(var i = 0; i < describes.length; i++) {
          var describe = describes[i];
          refToDescMap[describe.name] = {};
          for(var f = 0; f < describe.fields.length; f++) {
            var field = describe.fields[f];
            if(field.type == 'reference' && field.relationshipName) {
              refToDescMap[describe.name][field.relationshipName] = field;
            }
          }
        }
        callback();
      });
    },
    // get Layout metadata
    function(callback) {
      async.eachLimit(sObjects, 5, function(sObjectName, next) {
        self.conn.getListViews(sObjectName, function(err, res) {
          if(err) return next(err);
          if(Array.isArray(res.listviews) && res.listviews.length == 0) {
            // Filter custom setting
            return next();
          }
          sObjectListviews[sObjectName] = [];
          for(var i = 0; i < res.listviews.length; i++) {
            var listview = res.listviews[i];
            sObjectListviews[sObjectName].push({
              id : listview.id,
              developerName : listview.developerName,
              label : listview.label
            });
            ListviewReqQueues.push({ sobject : sObjectName, id : listview.id });
            // { listviewid : index }
            // describeListView只返回15位ID
            listviewIndexIdMap[listview.id.substring(0, 15)] = { sobject : sObjectName, index : i };
          }
          next();
        });
      },callback);
    },
    // get Listview describes
    function(callback) {
      async.eachLimit(ListviewReqQueues, 5, function(listviewQueue, next) {
        self.conn.describeListView(listviewQueue.sobject, listviewQueue.id, function(err, res) {
          if(err) return next(err);
          if(!res || !res.id) return next();
          var indexObj = listviewIndexIdMap[res.id.substring(0, 15)];
          // Filter reference column
          for(var i = 0; i < res.columns.length; i++) {
            var column = res.columns[i];
            var fieldNames = column.fieldNameOrPath.split('.');
            if(fieldNames.length == 1) continue;
            var refTo = fieldNames[0];  // eg. Account, Owner
            var refToDesc = refToDescMap[indexObj.sobject];
            if(!refToDesc) continue;
            if(!refToDesc.hasOwnProperty(refTo)) continue;
            var refToObjectName = refToDesc[refTo].referenceTo;
            refToObjectName = (Array.isArray(refToObjectName)) ? refToObjectName[0] : refToObjectName;
            res.columns[i]['referenceTo'] = refToObjectName;
            var refLabel = refToDesc[refTo].label;
            if(refLabel.endsWith(' ID') && !refToObjectName.endsWith('__c')) refLabel = T_Util.replaceLast(refLabel, ' ID', '');
            res.columns[i]['label'] = refLabel;
            if(sObjects.indexOf(refToObjectName) < 0 && refSObjects.indexOf(refToObjectName) < 0) {
              refSObjects.push(refToObjectName);  // 追加describeSObjects
            }
          }
          sObjectListviews[indexObj.sobject][indexObj.index]['describe'] = res;
          next();
        });
      },callback);
    }
  ], function(err) {
    if(err) return self.handleError(err);
    if(refSObjects.length > 0) {
      self.conn.describeSObjects(refSObjects, function(err, desc) {
        if(err) return self.handleError(err);
        describes = describes.concat(desc);
        return options.callback({ mode : 'requestListView', listviews : sObjectListviews, describes : describes });
      });
    } else {
      return options.callback({ mode : 'requestListView', listviews : sObjectListviews, describes : describes });
    }
    
  });
}

// request debuglog body
SD_App.prototype.requestDebuglog = function(options) {
  var self = this;
  options = options || {};
  if(options.resize) {
    // Resize when is loading
    self.view.resizePanel(options);
  }
  var existIds = options.existIds || [];
  var soql = 'SELECT Id, LogUser.Id, LogUser.Name, Operation, LastModifiedDate FROM ApexLog ORDER BY LastModifiedDate DESC';
  //console.log("soql: ", soql);
  self.conn.query(soql, function(err, res) {
    if(err) return self.handleError(err);
    var data = {};
    if(res.done !== true || res.records.length == 0) {
      return options.callback({ mode : 'requestDebuglog', data : data });
    }
    for(var i = 0; i < res.records.length; i++) {
      var record = res.records[i];
      // Do not request old log
      if(existIds.indexOf(record.Id) >= 0) continue;
      data[record.Id] = record;
    }
    //console.log("request data : ", existIds, data);
    if(Object.keys(data).length == 0) {
      // No new data
      return options.callback({ mode : 'requestDebuglog', data : data });
    }
    var tooling = self.conn.conn.tooling;
    async.eachLimit(Object.keys(data), 5, function(rId, next) {
      var url = '/sobjects/ApexLog/'+rId+'/Body/';
      //console.log("restPath url : ", url);
      var requestCallback = function(err, body){
        if (err) { console.error(err); }
        //console.log('/sobjects/ApexLog/response ' + rId, body.length);
        data[rId]['Body'] = body;
        next();
      };
      if(self.isLEX) {
        return self.sendCrobRequest({ method : 'tooling.request', params : url }, requestCallback);
      }
      tooling.request(url).thenCall(requestCallback);
    }, function(err) {
      if (err) { console.error(err); }
      return options.callback({ mode : 'requestDebuglog', data : data });
    });
  });
}


