// Open Query Viewer
SD_App.prototype.openQueryViewer = function(options) {
  var self = this;
  self.view.openQueryViewer(options);
}
// Open Field Viewer
SD_App.prototype.openFieldEditor = function(options) {
  var self = this;
  options['client'] = self;
  options['metadata'] = self.conn.metadata;
  options['metadataNameMap'] = self.conn.metadataNameMap;
  self.view.openFieldEditor(options);
}

/*-----------------------------------------------------------------*
 * Private Functions
 *-----------------------------------------------------------------*/
// 所在ページ情報を解析
SD_App.prototype.parsePage = function() {
  var self = this;
  var url = window.location.href;
  // Detect is Lightning
  var parser = T_Util.getParser(url);
  self.isLEX = T_Util.isLightningDomain(parser);
  var result = { 
    mode : 'init',
    url : url, 
    isLEX : self.isLEX,
    userInfo : self.userInfo
  };
  if(!self.conn.authed) {
    result['page'] = 'unauth';
    return result;
  }
  result['metadata'] = self.conn.metadata;
  var detailObj = self.checkDetail(url);
  if(detailObj) {
    var oId = T_Util.getObjectIdFromUri(url);
    self.sobject = detailObj;
    result['page'] = 'detail';
    result['sobject'] = detailObj;
    result['title'] = self.view.getRecordName({ isLEX : self.isLEX, sobject : self.sobject });
    result['id'] = oId;
    result['isApiNameShowed'] = self.view.isApiNameLabelShow({ isLEX : self.isLEX });
    return result;
  }
  var listObj = self.checkList(url);
  if(listObj) {
    self.sobject = listObj;
    result['page'] = 'list';
    result['title'] = self.view.getRecordName({ isLEX : self.isLEX, sobject : self.sobject });
    result['sobject'] = listObj;
    return result;
  }
  var settingObj = self.checkSetting(url);
  if(settingObj) {
    self.sobject = settingObj;
    result['page'] = 'setting';
    result['sobject'] = settingObj;
    return result;
  }
  var isDebugLogList = T_Util.isDebugLogListPage(parser);
  if(isDebugLogList) {
    result['page'] = 'debugloglist';
    return result;
  }
  var isDebugLogDetail = T_Util.isDebugLogDetailPage(parser);
  if(isDebugLogDetail && T_LogViewer) {
    result['page'] = 'debuglogdetail';
    result['master'] =  T_LogViewer.getEventMaster();
    result['lineTags'] = T_LogViewer.lineTags;
    return result;
  }
  result['page'] = 'other';
  return result;
}
// 詳細ページ
SD_App.prototype.checkDetail = function(url) {
  var self = this;
  var oId = T_Util.getObjectIdFromUri(url);
  if(T_Util.isBlank(oId)) return false;
  var prefix = oId.substring(0, 3);
  if(self.conn.metadataPrefixMap && self.conn.metadataPrefixMap.hasOwnProperty(prefix)) {
    return self.conn.metadataPrefixMap[prefix];
  }
  return false;
}

// リストページ
SD_App.prototype.checkList = function(url) {
  var self = this;
  if(self.isLEX) {
    // Support for lightning
    var parser = T_Util.getParser(url);
    var paths = parser.pathname.split('/');
    if(paths.length < 5) return null;
    if(paths[1] == 'lightning' && paths[2] == 'o' && paths[4] == 'list') {
      var sobjectName = paths[3];
      if(self.conn.metadataNameMap && self.conn.metadataNameMap.hasOwnProperty(sobjectName)) {
        return self.conn.metadataNameMap[sobjectName];
      }
    }
    return false;
  }

  var prefix = T_Util.getIdPreFixFromUri(url);
  if(T_Util.isBlank(prefix)) return false;
  if(self.conn.metadataPrefixMap && self.conn.metadataPrefixMap.hasOwnProperty(prefix)) {
    return self.conn.metadataPrefixMap[prefix];
  }
  return false;
}

// カスタム設定
SD_App.prototype.checkSetting = function(url) {
  var self = this;
  var oId = T_Util.getObjectIdFromUri(url);
  if(!T_Util.isBlank(oId) && self.conn.metadata) {
    //先验证是否Custom Object
    //Custom Object
    for(var k in self.conn.metadata){
      var sobj = self.conn.metadata[k];
      if(sobj.objectId == oId){
        return sobj;
      }
    }
  }

  //验证是否标准Object
  var parser = T_Util.getParser(url);
  if(self.isLEX) {
    // Lightning
    var paths = parser.pathname.split('/');
    if(paths.length < 6) return null;
    if(paths[1] == 'lightning' && paths[2] == 'setup' && paths[3] == 'ObjectManager') {
      var sobjectName = paths[4];
      if(self.conn.metadataNameMap && self.conn.metadataNameMap.hasOwnProperty(sobjectName)) {
        return self.conn.metadataNameMap[sobjectName];
      }
    }
    return false;
  }

  var sobjectName = T_Util.getParam('type', url);
  var setupid = T_Util.getParam('setupid', url);
  //log('>>>> sobjectName ' + sobjectName);
  if(T_Util.isBlank(sobjectName) || T_Util.isBlank(setupid)) return false;
  if(self.conn.metadataNameMap && self.conn.metadataNameMap.hasOwnProperty(sobjectName)) {
    return self.conn.metadataNameMap[sobjectName];
  }
  return false;
}

// Get related sobjects from layout metadata
SD_App.prototype.getRelatedObjFromLayout = function(metadata) {
  var self = this;
  var relatedObjects = [];
  if(T_Util.isBlank(metadata)) return relatedObjects;

  for(var i = 0; i < metadata.length; i ++){
    var layout = metadata[i];
    if(T_Util.isBlank(layout.relatedLists)) continue;
    for(var l = 0; l < layout.relatedLists.length; l ++){
      var relatedList = layout.relatedLists[l];
      // Get object from releatedlist name
      var sobjectName = relatedList.sobject;
      if(relatedObjects.indexOf(sobjectName) < 0) {
        relatedObjects.push(sobjectName);
      }

      /*if(T_Util.isBlank(relatedList.fields)) continue;
      for(var f = 0; f < relatedList.fields.length; f ++){
        var field = relatedList.fields[f];
        var objs = field.split('.');
        if(objs.length == 0) continue;
        var sobjectName = objs[0];
        // Filter dumplicate
        if(!objectUppNameMap.hasOwnProperty(sobjectName)) continue;
        sobjectName = objectUppNameMap[sobjectName];  // ACCOUNT ⇒ Account
        if(relatedObjects.indexOf(sobjectName) < 0) {
          relatedObjects.push(sobjectName);
        }
      }*/
    }
  }
  return relatedObjects;
}

SD_App.prototype.handleError = function(err) {
  if(err && this.conn.handleOAuthError(err)) {
    var mes = 'SESSION EXPIRED, reopen Salesforce DevTools and try again.';
    alert(mes);
    return;
  }
  var mes = 'Oops! It seems some error occurred, report issue on \n https://github.com/xgeek-net/salesforce-devtools/issues';
     mes += '\n[ERROR]' + JSON.stringify(err);
  alert(mes);
}